import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { TILE_HEIGHT_LIMITS, TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import { isNearWorldCenter } from '@/features/WorldGenerator/helpers/center/isNearCenter';
import { findMinHeight } from '@/features/WorldGenerator/helpers/findMinHeight';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { isRandomChance } from '@/libs/random/isRandomChance';

export const generateNewLake: TGenerator = (base, options) => ({
    ...base,
    height: findMinHeight(options.neighbors) || base.height,
    biomeTile: {
        id: guid(),
        key: EBiomeTile.Lake,
        size: randomizeBiomeSize([0.2, 0.5, 0.2, 0.1]),
    },
});

export const generateLake: TGenerator = (base, options) => {
    if (isNearWorldCenter(base.coords)) {
        return base;
    }

    const seas = findBiomeNeighbors(EBiomeTile.Sea, options);

    if (seas.length) {
        return base;
    }

    const lakes = findBiomeNeighbors(EBiomeTile.Lake, options);

    if (lakes.some((lake) => lake.diff <= 0)) {
        return base;
    }

    const [lake] = lakes;

    const heightDiff = TILE_HEIGHT_LIMITS[1] / 2 - Math.abs(base.height - TILE_SEA_LEVEL);

    let chance = heightDiff * 0.001;
    if (lake?.size === 1) {
        chance = 1;
    } else if (lakes.length && lakes.length !== 1) {
        chance = 0.5 * lakes.length;
    } else if (lake) {
        chance = 0;
    }

    if (!isRandomChance(chance)) {
        return base;
    }

    if (lake) {
        return {
            ...base,
            height: lake.tile.height,
            biomeTile: {
                id: lake.biome.id,
                key: EBiomeTile.Lake,
                size: lake.biome.size,
            },
        };
    }

    return generateNewLake(base, options);
};
