import { CustomBlur } from '@/features/CustomPostProcess/components/CustomBlur';
import { TComponentClass } from '@/libs/cocos/types/TComponentClass';
import { gfx, renderer, rendering, postProcess } from 'cc';

const FORMAT = gfx.Format.RGBA8;

export class CustomBlurPass<T extends CustomBlur> extends postProcess.SettingPass {
    private settingClass: TComponentClass<T>;

    constructor(settingClass: TComponentClass<T>) {
        super();
        this.settingClass = settingClass;
    }

    get setting() {
        return this.getSetting(this.settingClass);
    }

    checkEnable(camera: renderer.scene.Camera) {
        let enable = super.checkEnable(camera);
        if (postProcess.disablePostProcessForDebugView()) {
            enable = false;
        }
        return enable && this.setting.material != null;
    }

    name = 'CustomBlurPass';
    outputNames = ['CustomBlurMap'];

    public render(camera: renderer.scene.Camera, _ppl: rendering.Pipeline): void {
        const setting = this.setting;

        if (!setting.material) {
            return;
        }

        let passContext = this.context;
        passContext.material = setting.material;

        const cameraID = this.getCameraUniqueID(camera);

        passContext.clearBlack();

        let input = this.lastPass!.slotName(camera, 0);
        for (let i = 0; i < setting.iterations; ++i) {
            passContext
                .updatePassViewPort()
                .addRenderPass(`blur-x`, `blur-x${cameraID}`)
                .setPassInput(input, 'outputResultMap')
                .addRasterView('CustomBlurMap_TMP', FORMAT)
                .blitScreen(0)
                .version();

            passContext
                .updatePassViewPort()
                .addRenderPass(`blur-y`, `blur-y${cameraID}`)
                .setPassInput(input, 'outputResultMap')
                .setPassInput('CustomBlurMap_TMP', 'outputResultMap')
                .addRasterView(this.slotName(camera), FORMAT)
                .blitScreen(1)
                .version();

            input = this.slotName(camera);
        }
    }
}
