import { WorldCamera } from '@/features/WorldCamera/components/WorldCamera';
import { WorldUi } from '@/features/WorldUi/components/WorldUi';
import { TComponentClass } from '@/libs/cocos/types/TComponentClass';
import { ccclass, property } from '@/libs/decorators/cocos';
import { Component } from 'cc';

const DI_MAP_DATA: [TComponentClass<Component>, keyof DiWorld][] = [
    [WorldCamera, 'worldCamera'],
    [WorldUi, 'worldUi'],
];

const DI_MAP = new Map(DI_MAP_DATA);

@ccclass('DiWorld')
export class DiWorld extends Component {
    static instance: DiWorld | null = null;

    constructor(name: string) {
        super(name);

        DiWorld.instance = this;
    }

    @property(WorldCamera)
    worldCamera: WorldCamera;

    @property(WorldUi)
    worldUi: WorldUi;

    static get<T extends Component>(Comp: TComponentClass<T>): T {
        if (!DiWorld.instance) {
            throw new Error('Di is not initalized!');
        }

        const key = DI_MAP.get(Comp);

        if (!key) {
            throw new Error(`Di key ${key} do not exist!`)
        }

        return DiWorld.instance[key] as T;
    }

    static get worldCamera() {
        return DiWorld.get(WorldCamera);
    }

    static get worldUi() {
        return DiWorld.get(WorldUi);
    }
}
