import { toFixedNumber } from '@/libs/math/toFixedNumber';

export function randomFromRange(range: number[], digits = 0) {
    const [min, max] = range;

    if (!digits) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    return toFixedNumber(Math.random() * (max - min) + min, digits);
}
