import type { Node } from 'cc';
import { action, observable } from 'vendors/mobx';
import type { Tile } from '@/features/Tile/components/Tile';
import { Observer } from '@/libs/cocos/components/Observer';
import type { ITapEvent } from '@/libs/cocos/types/ITapEvent';

export interface IBiomeProps<T> {
    data: T;
    tile: Tile;
    onTap?: (event: ITapEvent) => void;
}

export abstract class Biome<T> extends Observer {
    protected renderNode: Node | null = null;

    @observable data: T;

    tile: Tile;

    protected onTap?: (event: ITapEvent) => void;

    @action.bound
    initialize({ data, tile, onTap }: IBiomeProps<T>) {
        this.data = data;
        this.tile = tile;
        this.onTap = onTap;
    }

    protected abstract createRenderNode(): Node | null;

    render() {
        if (this.renderNode) {
            this.renderNode.destroy();
            this.renderNode = null;
        }

        this.renderNode = this.createRenderNode();
    }
}
