import { getAllComponents } from '@/libs/cocos/helpers/getAllComponents';
import type { Node } from 'cc';
import { MeshCollider, MeshRenderer } from 'cc';

interface IAddMeshColliderOptions {
    isTrigger?: boolean;
}

export function addMeshCollider(node: Node, options: IAddMeshColliderOptions) {
    const meshes = getAllComponents(node, MeshRenderer);

    const colliders: MeshCollider[] = [];

    for (const mesh of meshes) {
        const collider = mesh.node.addComponent(MeshCollider);

        if (mesh) {
            collider.mesh = mesh.mesh;
        }

        collider.isTrigger = Boolean(options.isTrigger);

        colliders.push(collider);
    }

    return colliders;
}
