import { TComponentClass } from '@/libs/cocos/types/TComponentClass';
import type { Component } from 'cc';
import { Node } from 'cc';

interface IComponent<P> extends Component {
    initialize(props: P): void;
}

export function factoryComponent<P, C extends IComponent<P>>(Comp: TComponentClass<C>, parent: Node, props: P) {
    const node = new Node();

    node.layer = parent.layer;

    const component = node.addComponent(Comp);

    (component as any).initialize?.(props);

    parent.addChild(node);

    return component;
}
