import type { Material } from 'cc';
import type { ITapEvent } from '@/libs/cocos/types/ITapEvent';
import { ccclass } from '@/libs/decorators/cocos';
import { bound } from '@/libs/decorators/bound';
import { BuildingComponent, IBuildingComponentProps } from '@/features/Building/components/BuildingComponent';
import { cached } from '@/libs/decorators/cached';
import { action, computed } from 'vendors/mobx';

export interface IBuildingProps extends IBuildingComponentProps {
    onTap?: (event: ITapEvent) => void;
}

@ccclass('Building')
export class Building extends BuildingComponent {
    protected onTap?: (event: ITapEvent) => void;

    @computed
    get buildingData() {
        return this.data;
    }

    @cached
    get materials(): Material[] {
        return [];
    }

    @bound
    onBuildingTap(event: ITapEvent) {
        this.onTap?.(event);
    }

    @action.bound
    initialize({ data, tile, onTap }: IBuildingProps) {
        super.initialize({ data, tile });
        this.onTap = onTap;
    }
}
