import { Observer } from '@/libs/cocos/components/Observer';
import { ccclass, property } from '@/libs/decorators/cocos';
import { tween } from 'cc';

@ccclass('UiActiveAnimate')
export class UiActiveAnimate extends Observer {
    @property
    from = false;

    @property
    to = true;

    @property
    duration = 1;

    @property
    delay = 0;

    onLoad() {
        this.node.active = this.from;

        tween(this.node)
            .delay(this.delay)
            .to(this.duration, {})
            .call(() => (this.node.active = this.to))
            .start();
    }
}
