import { waterTiles } from '@/features/Biome/data/waterTiles';
import type { IBiomeTileData } from '@/features/Biome/types/IBiomeData';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import type { IGeneratorOptions } from '@/features/WorldGenerator/types/IGeneratorOptions';
import type { INeigborBiome } from '@/features/WorldGenerator/types/INeigborBiome';

export function findWaterNeighbors(options: IGeneratorOptions) {
    return waterTiles.reduce<INeigborBiome<IBiomeTileData>[]>(
        (result, biome) => [...result, ...findBiomeNeighbors<IBiomeTileData>(biome, options)],
        [],
    );
}
