import { Photobooth } from '@/features/Photobooth/components/Photobooth';
import { Observer } from '@/libs/cocos/components/Observer';
import { createPrefabNode } from '@/libs/cocos/helpers/createPrefabNode';
import { factorySprite } from '@/libs/cocos/helpers/factorySprite';
import { ccclass, property } from '@/libs/decorators/cocos';
import { Prefab, Node, Sprite, CCFloat, SpriteFrame } from 'cc';
import { autorun, computed, observable, runInAction } from 'vendors/mobx';

const SHOOT_CACHE = observable.map<string, SpriteFrame>();

@ccclass('PhotoboothIcon')
export class PhotoboothIcon extends Observer {
    spriteComponent: Sprite | null = null;

    @observable.ref sprite: SpriteFrame | null = null;

    @property({
        type: Prefab,
        visible(this: PhotoboothIcon) {
            return !this.shootNode;
        },
    })
    @observable.ref
    shootPrefab: Prefab | null = null;

    @property({
        type: Node,
        visible(this: PhotoboothIcon) {
            return !this.shootPrefab;
        },
    })
    @observable.ref
    shootNode: Node | null = null;

    @property(CCFloat)
    scale: number = 1;

    createShootTarget() {
        if (this.shootNode) {
            return this.shootNode;
        }

        if (this.shootPrefab) {
            const prefabNode = createPrefabNode(this.shootPrefab);

            return prefabNode;
        }

        return null;
    }

    @computed
    get cacheKey() {
        return this.shootNode?.uuid || this.shootPrefab?.uuid;
    }

    @computed
    get cachedSprite() {
        return (this.cacheKey && SHOOT_CACHE.get(this.cacheKey)) || null;
    }

    onLoad() {
        this.spriteComponent = factorySprite(this.node, this.cachedSprite || this.sprite);
    }

    disposeSpriteShoot = autorun(async () => {
        if (this.cachedSprite) {
            return;
        }

        const node = this.createShootTarget();

        if (node) {
            const sprite = await Photobooth.factory()?.capture(node, { scale: this.scale });

            if (sprite) {
                runInAction(() => {
                    this.sprite = sprite;

                    if (this.cacheKey) {
                        SHOOT_CACHE.set(this.cacheKey, sprite);
                    }
                });
            }
        }
    });

    render() {
        const sprite = this.cachedSprite || this.sprite;

        if (this.spriteComponent && this.spriteComponent.spriteFrame !== sprite) {
            this.spriteComponent.spriteFrame = sprite;
        }
    }
}
