import { EBiomeSize } from '@/features/Biome/data/EBiomeSize';
import type { IChance } from '@/libs/random/IChance';
import { randomChance } from '@/libs/random/randomChance';

export function randomizeBiomeSize(chances: number[]) {
    const [tinyChance, smallChance, mediumChange, largeChance] = chances;

    const randomize: IChance<() => EBiomeSize>[] = [
        {
            name: 'large',
            chance: () => largeChance || 0,
            fn: () => EBiomeSize.Large,
        },
        {
            name: 'medium',
            chance: () => mediumChange || 0,
            fn: () => EBiomeSize.Medium,
        },
        {
            name: 'small',
            chance: () => smallChance || 0,
            fn: () => EBiomeSize.Small,
        },
        {
            name: 'tiny',
            chance: () => tinyChance || 0,
            fn: () => EBiomeSize.Tiny,
        },
    ];

    return randomChance(randomize)();
}
