import { cached } from '@/libs/decorators/cached';
import { Hexagon } from '@/libs/hexagon/Hexagon';

export class HexagonTile<T> extends Hexagon<T> {
    @cached
    get right() {
        return this.coords.clone().add3f(1, 1, 0);
    }

    @cached
    get topRight() {
        return this.coords.clone().add3f(0, 1, 1);
    }

    @cached
    get topLeft() {
        return this.coords.clone().add3f(-1, 0, 1);
    }

    @cached
    get left() {
        return this.coords.clone().add3f(-1, -1, 0);
    }

    @cached
    get bottomLeft() {
        return this.coords.clone().add3f(0, -1, -1);
    }

    @cached
    get bottomRight() {
        return this.coords.clone().add3f(1, 0, -1);
    }
}
