import type { Material, Node } from 'cc';
import { MeshRenderer, Vec3 } from 'cc';
import { action, computed, observable } from 'vendors/mobx';
import type { IBuildingData } from '@/features/Building/types/IBuildingData';
import type { Tile } from '@/features/Tile/components/Tile';
import { Observer } from '@/libs/cocos/components/Observer';
import { createPrefabNode } from '@/libs/cocos/helpers/createPrefabNode';
import type { ITapEvent } from '@/libs/cocos/types/ITapEvent';
import { assets } from '@/features/Assets/data/assets';
import { BuildingsDB } from '@/features/Building/data/BuildingsDB';
import { BUILD_PROGRESS_MAX, BUILD_PROGRESS_STAGES, BUILDING_ROTATION_INIT } from '@/features/Building/data/consts';
import { clamp } from '@/libs/math/clamp';
import { getAllComponents } from '@/libs/cocos/helpers/getAllComponents';
import { IUi3DProps, Ui3D } from '@/features/Ui/components/Ui3D';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';

const UI_3D_BUTTON_BORDERS = [1, 10] as const;

export interface IBuildingComponentProps {
    data: IBuildingData;
    tile: Tile;
}

export abstract class BuildingComponent extends Observer {
    protected building?: Node;
    protected turnsLeftUi?: Ui3D;

    @observable protected data: IBuildingData;

    tile: Tile;

    @computed
    get buildingKey() {
        return this.buildingData.key;
    }

    @computed
    get isConstructing() {
        const { progress } = this.buildingData;

        return Boolean(progress < BUILD_PROGRESS_MAX);
    }

    @computed
    get constructionSpeed() {
        const { buildOptions } = this.buildingDb;

        return BUILD_PROGRESS_MAX / (buildOptions ? buildOptions.turns : BUILD_PROGRESS_MAX);
    }

    @computed
    get constructionStepsLeft() {
        if (!this.isConstructing) {
            return 0;
        }

        const { progress } = this.buildingData;

        const turnsLeft = (BUILD_PROGRESS_MAX - progress) / this.constructionSpeed;

        return Math.ceil(parseFloat(turnsLeft.toFixed(2)));
    }

    @computed
    get prefab() {
        let { prefab } = this.buildingDb;

        if (this.isConstructing) {
            const { progress } = this.buildingData;
            const step = BUILD_PROGRESS_MAX / BUILD_PROGRESS_STAGES.length;
            const stageIndex = clamp(Math.floor(progress / step), [0, BUILD_PROGRESS_STAGES.length - 1]);

            prefab = BUILD_PROGRESS_STAGES[stageIndex];
        }

        return assets.getPrefabs(prefab);
    }

    @computed
    get rotation() {
        return new Vec3(0, BUILDING_ROTATION_INIT + (this.buildingData.rotate || 0), 0);
    }

    abstract get buildingData(): IBuildingData;

    abstract get materials(): Material[];

    @computed
    get buildingDb() {
        return BuildingsDB[this.buildingKey];
    }

    @computed
    get buildingHeight() {
        if (!this.prefab || !this.building) {
            return 0;
        }

        const meshes = getAllComponents(this.building, MeshRenderer);

        const [min, max] = meshes.reduce<[number | null, number | null]>(
            ([min, max], mesh) => {
                const { model } = mesh;

                const bounds = model?.worldBounds;

                if (bounds) {
                    const minVec = new Vec3();
                    const maxVec = new Vec3();

                    bounds.getBoundary(minVec, maxVec);

                    return [
                        min !== null ? Math.min(min, minVec.y) : minVec.y,
                        max !== null ? Math.max(max, maxVec.y) : maxVec.y,
                    ];
                }

                return [min, max];
            },
            [null, null],
        );

        return (max || 0) - (min || 0);
    }

    abstract onBuildingTap(event: ITapEvent): void;

    render() {
        this.building?.destroy();
        this.building = undefined;

        this.building = createPrefabNode(this.prefab, {
            parent: this.node,
            rotation: this.rotation,
            materials: this.materials,
            castShadows: true,
            receiveShadows: true,
            onTap: this.onBuildingTap.bind(this),
        });

        if (this.isConstructing) {
            const props: IUi3DProps = {
                sprite: `BubbleTurns${clamp(this.constructionStepsLeft, UI_3D_BUTTON_BORDERS)}` as TResourceSprite,
                onTap: this.onBuildingTap,
                position: new Vec3(0, this.buildingHeight, 0),
            };

            if (!this.turnsLeftUi) {
                this.turnsLeftUi = factoryComponent(Ui3D, this.node, props);
            } else {
                this.turnsLeftUi.initialize(props);
            }
        } else {
            if (this.turnsLeftUi) {
                this.turnsLeftUi.node.destroy();
                this.turnsLeftUi = undefined;
            }
        }
    }

    onLoad() {
        this.node.setPosition(new Vec3(0, this.tile.groundY, 0));
    }

    @action.bound
    initialize({ data, tile }: IBuildingComponentProps) {
        this.data = data;
        this.tile = tile;
    }
}
