import { Font, Material, Prefab, SpriteFrame } from 'cc';
import { loadResourcesDir } from '@/libs/cocos/helpers/loadResourcesDir';
import { bound } from '@/libs/decorators/bound';
import { prepareMaterial } from '@/libs/helpers/prepareMaterial';

const LOAD_METHOD_REGEX = /^load/;

class Assets {
    wait: Promise<void>;

    private materials = new Map<TResourceMaterial, Material>();

    private sprites = new Map<TResourceSprite, SpriteFrame>();

    private prefabs = new Map<TResourcePrefab, Prefab>();

    private fonts = new Map<TResourceFont, Font>();

    constructor() {
        this.wait = this.waitAssets().catch((err) => console.error(err));
    }

    private get<K, V>(map: Map<K, V>, names: K): NonNullable<V>;
    private get<K, V>(map: Map<K, V>, names: K[]): NonNullable<V>[];
    private get<K, V>(map: Map<K, V>, names: OneOrMore<K>): OneOrMore<NonNullable<V>> {
        const get = (name: K) => {
            const resource = map.get(name);

            if (!resource) {
                throw new Error(`Cannot find resource asset with name ${name}`);
            }

            return resource;
        };

        if (Array.isArray(names)) {
            return names.map((name) => get(name));
        }

        return get(names);
    }

    getMaterials(names: TResourceMaterial): Material;
    getMaterials(names: TResourceMaterial[]): Material[];
    getMaterials(names: OneOrMore<TResourceMaterial>): OneOrMore<Material> {
        return this.get(this.materials, names);
    }

    getSprites(names: TResourceSprite): SpriteFrame;
    getSprites(names: TResourceSprite[]): SpriteFrame[];
    getSprites(names: OneOrMore<TResourceSprite>): OneOrMore<SpriteFrame> {
        return this.get(this.sprites, names);
    }

    getPrefabs(names: TResourcePrefab): Prefab;
    getPrefabs(names: TResourcePrefab[]): Prefab[];
    getPrefabs(names: OneOrMore<TResourcePrefab>): OneOrMore<Prefab> {
        return this.get(this.prefabs, names);
    }

    getFonts(names: TResourceFont): Font;
    getFonts(names: TResourceFont[]): Font[];
    getFonts(names: OneOrMore<TResourceFont>): OneOrMore<Font> {
        return this.get(this.fonts, names);
    }

    private async waitAssets() {
        const promises: Promise<void>[] = [];

        const methods = [
            ...Object.getOwnPropertyNames(this),
            ...Object.getOwnPropertyNames(Object.getPrototypeOf(this)),
        ] as (keyof Assets)[];

        methods.forEach((method) => {
            if (LOAD_METHOD_REGEX.test(method)) {
                const func = this[method] as any as (() => Promise<void>) | undefined;

                if (func) {
                    promises.push(func());
                }
            }
        });

        await Promise.all(promises);
    }

    @bound
    protected async loadMaterials() {
        const materials = (await loadResourcesDir('materials', Material)).map(prepareMaterial);

        for (const material of materials) {
            this.materials.set(material.name as TResourceMaterial, material);
        }
    }

    @bound
    protected async loadSprites() {
        const sprites = await loadResourcesDir('ui', SpriteFrame);

        for (const sprite of sprites) {
            this.sprites.set(sprite.name as TResourceSprite, sprite);
        }
    }

    @bound
    protected async loadPrefabs() {
        const prefabs = await loadResourcesDir('prefabs', Prefab);

        for (const prefab of prefabs) {
            this.prefabs.set(prefab.data.name as TResourcePrefab, prefab);
        }
    }

    @bound
    protected async loadFonts() {
        const fonts = await loadResourcesDir('fonts', Font);

        for (const font of fonts) {
            this.fonts.set(font.name as TResourceFont, font);
        }
    }
}

export const assets = new Assets();
