import type { Vec3 } from 'cc';
import { EBuilding } from '@/features/Building/data/EBuilding';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { WORLD_CENTER_HEIGHT } from '@/features/World/data/consts';
import { generateTileBaseHeight } from '@/features/WorldGenerator/helpers/generateTileBaseHeight';

export function generateWorldCenterTile(coords: Vec3): ITileData {
    return {
        coords,
        height: WORLD_CENTER_HEIGHT,
        baseHeight: generateTileBaseHeight(),
        building: {
            key: EBuilding.Castle,
            progress: 1,
        },
    };
}
