import { ELayer } from '@/libs/cocos/data/ELayer';
import { factoryChildNode } from '@/libs/cocos/helpers/factoryChildNode';
import { factorySprite } from '@/libs/cocos/helpers/factorySprite';
import { cached } from '@/libs/decorators/cached';
import { ccclass, property } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';
import { UITransform, Color, SpriteFrame, Widget, Component } from 'cc';

interface UiBackgroundProps {
    startSprite: SpriteFrame;
    endSprite: SpriteFrame;
    width?: number;
    height?: number;
    color: Color;
    isVertical?: boolean;
    castShadow?: boolean;
}

@ccclass('UiBackground')
export class UiBackground extends Component implements UiBackgroundProps {
    @property(SpriteFrame)
    startSprite: SpriteFrame;

    @property(SpriteFrame)
    endSprite: SpriteFrame;

    @property(Color)
    color: Color = Color.WHITE.clone();

    width?: number;

    height?: number;

    @property
    isVertical = false;

    @property
    castShadow = false;

    @cached
    get uiTransform() {
        return this.getComponent(UITransform);
    }

    get realWidth() {
        return this.uiTransform?.width || this.width || 0;
    }

    get realHeight() {
        return this.uiTransform?.height || this.height || 0;
    }

    addBackgroundChild(spriteFrame: SpriteFrame, options: { size: number; alignStart?: number; alignEnd?: number }) {
        const { isVertical } = this;
        const { size, alignStart, alignEnd } = options;

        const node = factoryChildNode(this.node);

        const uiTransform = node.addComponent(UITransform);

        if (isVertical) {
            uiTransform.width = this.realWidth;
            uiTransform.height = size;
        } else {
            uiTransform.width = size;
            uiTransform.height = this.realHeight;
        }

        const widget = node.addComponent(Widget);

        if (isVertical) {
            widget.isAlignLeft = true;
            widget.isAlignRight = true;

            widget.left = 0;
            widget.right = 0;

            if (alignStart !== undefined) {
                widget.isAlignTop = true;
                widget.top = alignStart;
            }

            if (alignEnd !== undefined) {
                widget.isAlignBottom = true;
                widget.bottom = alignEnd;
            }
        } else {
            widget.isAlignTop = true;
            widget.isAlignBottom = true;

            widget.top = 0;
            widget.bottom = 0;

            if (alignStart !== undefined) {
                widget.isAlignLeft = true;
                widget.left = alignStart;
            }

            if (alignEnd !== undefined) {
                widget.isAlignRight = true;
                widget.right = alignEnd;
            }
        }

        widget.updateAlignment();

        factorySprite(node, spriteFrame, this.color);

        return node;
    }

    onLoad() {
        if (this.castShadow) {
            this.node.layer = ELayer.UI_2D_SHADOW_CAST;
        }

        if (!this.getComponent(Widget)) {
            const widget = this.node.addComponent(Widget);

            widget.isAlignLeft = widget.isAlignRight = widget.isAlignBottom = widget.isAlignTop = true;
            widget.left = widget.right = widget.bottom = widget.top = 0;
            widget.updateAlignment();
        }

        const center = assets.getSprites('BgPixel');

        const { startSprite, endSprite, realWidth, realHeight, isVertical } = this;

        const startSize = isVertical ? startSprite.rect.height : startSprite.rect.width;
        const endSize = isVertical ? endSprite.rect.height : endSprite.rect.width;
        const realSize = isVertical ? realHeight : realWidth;

        this.addBackgroundChild(startSprite, {
            size: startSize,
            alignStart: 0,
        });

        this.addBackgroundChild(center, {
            size: Math.max(realSize - startSize - endSize, 0),
            alignStart: startSize,
            alignEnd: endSize,
        });

        this.addBackgroundChild(endSprite, {
            size: endSize,
            alignEnd: 0,
        });
    }

    initialize(props: UiBackgroundProps) {
        Object.assign(this, props);
    }
}
