import type { IVec3Like } from 'cc';
import { Vec3 } from 'cc';
import { cached } from '@/libs/decorators/cached';

export type HexagonClass<T, H extends Hexagon<T>> = { new (self: T, coords: Vec3, options: HexagonOptions): H };

export interface HexagonOptions {
    hexHeight: number;
    hexRadius: number;
}

export abstract class Hexagon<T> {
    self: T;

    coords: Vec3;

    hexHeight: number;

    hexRadius: number;

    constructor(self: T, coords: Vec3, { hexHeight, hexRadius }: HexagonOptions) {
        this.self = self;
        this.coords = coords;
        this.hexHeight = hexHeight;
        this.hexRadius = hexRadius;
    }

    abstract get right(): Vec3;
    abstract get topRight(): Vec3;
    abstract get topLeft(): Vec3;
    abstract get left(): Vec3;
    abstract get bottomLeft(): Vec3;
    abstract get bottomRight(): Vec3;

    @cached
    get neighbors() {
        return [this.right, this.topRight, this.topLeft, this.left, this.bottomLeft, this.bottomRight];
    }

    @cached
    get positionX() {
        const { x } = this.coords;
        const absX = Math.abs(x);

        return Math.sign(x) * this.hexRadius * (absX + (Math.floor(absX - 1) + 1) / 2);
    }

    @cached
    get positionZ() {
        const { y, z } = this.coords;

        return (y + z) * this.hexHeight;
    }

    @cached
    get key() {
        return this.coords.toString();
    }

    getNeighbor(coords: IVec3Like) {
        return this.neighbors.find((neighbor) => Vec3.equals(neighbor, coords));
    }

    getOrderedNeigbors(from: IVec3Like | null) {
        const fromIndex = from ? this.neighbors.findIndex((neighbor) => Vec3.equals(neighbor, from)) : -1;

        if (fromIndex < 0) {
            return this.neighbors.slice();
        }

        const start = this.neighbors.slice(fromIndex);
        const end = this.neighbors.slice(0, fromIndex);

        return [...start, ...end];
    }
}
