export enum EBiomeObject {
    Forest = 'forest',
    ForestRocks = 'forest-rocks',
    Mountain = 'mountain',
    Rocks = 'rocks',
    Hill = 'hill',
}

export enum EBiomeTile {
    Clay = 'clay',
    Sand = 'sand',
    Sea = 'sea',
    River = 'river',
    Lake = 'lake',
}

export const BIOME_OBJECT_KEYS = Object.values(EBiomeObject);

export const BIOME_TILE_KEYS = Object.values(EBiomeTile);

export type EBiome = EBiomeObject | EBiomeTile;
