import { Component } from 'cc';
import { Node } from 'cc';
import { ccclass, property } from '@/libs/decorators/cocos';
import { router } from '@/routes/router';
import { Ui } from '@/features/Ui/components/Ui';
import { factoryTemplateComponent } from '@/libs/cocos/helpers/factoryTemplateComponent';

@ccclass('WorldUi')
export class WorldUi extends Ui {
    @property(Node)
    overlayNode: Node;

    @property(Node)
    shadowNode: Node;

    @property(Node)
    headerNode: Node;

    @property(Node)
    bodyNode: Node;

    @property(Component)
    screen: Component | null;

    updateUiOverlay() {
        this.overlayNode.active = router.route.hasUiOverlay;
    }

    updateUiHeader() {
        this.headerNode.active = router.route.hasUiHeader;
    }

    updateUiBody() {
        const { screen: ScreenClass } = router.route;

        if (!this.screen || !(this.screen instanceof ScreenClass)) {
            this.screen?.node.destroy();

            this.screen = factoryTemplateComponent(ScreenClass, this.bodyNode, {});
        }
    }

    render() {
        this.updateUiOverlay();
        this.updateUiHeader();
        this.updateUiBody();
    }
}
