import { Button as _Button, EventHandler, Label } from 'cc';
import { Observer } from '@/libs/cocos/components/Observer';
import { cached } from '@/libs/decorators/cached';

export abstract class Button extends Observer {
    @cached
    get button() {
        return this.node.getComponent(_Button);
    }

    get label() {
        return this.node.getComponentInChildren<Label>(Label);
    }

    onLoad() {
        if (!this.button) {
            throw new Error('Button component must be used only with buttons');
        }

        const clickEventHandler = new EventHandler();
        clickEventHandler.target = this.node;
        clickEventHandler.component = (this as any).__classname__;
        clickEventHandler.handler = 'onClick';

        this.button.clickEvents.push(clickEventHandler);
    }

    abstract onClick(event: Event): void;
}
