export const LIMITS = [-1, 1] as const; // zoom and rotation can be from -1 to 1
export const CAMERA_POSITION_OFFSET = 15; // difference in position for camera Y/Z
export const CAMERA_ROTATION_OFFSET = 10; // degrees for zoom difference in camera Y rotation

export const TARGET_ROTATE_OFFSET = 180; // half the circle, for rotation from -180 to 180

export const MOVE_DIVIDER = 14; // if biger, then faster
export const ZOOM_MOVE_INFLUENCE = 8; // different zooms levels multiplies speed
export const DOUBLE_TOUCH_TIME = 250; // ms to activate double touch type

export const MOVE_SAFEZONE = 20; // safe zone from farthest chunk

export const IMPULSE_DURATION = 4; // seconds, duration of move implulse animation
export const IMPULSE_STRENGTH = 1.5; // we multiply touch impulse by that strength
export const IMPULSE_LENGTH = 0.05; // delta vector must have such length to activate impulse

export const DEFAULT_NAVIGATE_DURATION = 1; // seconds, default navigation time

export const ROTATE_SPEED = 8; // the bigger - the slower is rotate

export const FIXED_ROTATE_LIMIT = 5; // we convert angles with toFixed()
export const FIXED_POSITION_LIMIT = 20; // we convert position with toFixed()

export const TAP_MAX_MOVED_TIMES = 50; // if we move faster than this, we will not tap
export const TAP_TIME = 100; // if we released touch event fewer than this, we will tap

export const MAX_TOUCH_SIZE = 720; // same as x2 base screen width
