import { CustomBlurPass } from '@/features/CustomPostProcess/data/CustomBlurPass';
import { _decorator, Material, EffectAsset, rendering, Vec4, postProcess, RenderTexture, director, Director, game, Game } from 'cc';

const { ccclass, property, menu, executeInEditMode } = _decorator;

@ccclass('CustomBlur')
@menu('PostProcess/CustomBlur')
@executeInEditMode
export class CustomBlur extends postProcess.PostProcessSetting {
    @property(RenderTexture)
    mask: RenderTexture | undefined;

    @property(EffectAsset)
    private _effectAsset: EffectAsset | undefined;

    get effect() {
        return this._effectAsset;
    }

    @property(EffectAsset)
    set effect(value) {
        this._effectAsset = value;

        if (!this._effectAsset) {
            this._material?.destroy();
            this._material = null;
        } else {
            if (this._material) {
                this._material.reset({ effectAsset: this._effectAsset });
            } else {
                this._material = new Material();
                this._material.initialize({ effectAsset: this._effectAsset });
            }

            this.updateMaterial();
        }
    }

    @property
    iterations = 3;

    @property
    set blurRadius(value) {
        this._blurParams.x = value;
        this.updateMaterial();
    }

    get blurRadius() {
        return this._blurParams.x;
    }

    private _material: Material | null = null;

    public get material(): Material | null {
        return this._material;
    }

    private _blurParams: Vec4 = new Vec4(4.0, 0.0, 0.0, 0.0);

    public get blurParams(): Vec4 {
        return this._blurParams;
    }

    updateMaterial() {
        if (!this._material) {
            return;
        }

        if (this.mask) {
            this._material.setProperty('maskTexture', this.mask);
        }

        this._material.setProperty('blurParams', this.blurParams);
    }

    protected start(): void {
        if (!this._material) {
            this._material = new Material();
            this._material.initialize({ effectAsset: this._effectAsset });
            this.updateMaterial();
        }

        let builder = rendering.getCustomPipeline('Custom') as postProcess.PostProcessBuilder;

        if (builder) {
            builder.insertPass(new CustomBlurPass(CustomBlur), postProcess.BlitScreenPass);
        }
    }
}
