import { EEventName } from '@/libs/cocos/data/EEventName';
import { addMeshCollider } from '@/libs/cocos/helpers/addMeshCollider';
import { ITapEvent } from '@/libs/cocos/types/ITapEvent';
import type { Material, Prefab, Node, Vec3 } from 'cc';
import { MeshRenderer, instantiate } from 'cc';

interface ICreatePrefabOptions {
    materials?: Material[];
    position?: Vec3;
    parent?: Node;
    scale?: Vec3;
    rotation?: Vec3;
    castShadows?: boolean;
    receiveShadows?: boolean;
    onTap?: (event: ITapEvent) => void;
}

export function createPrefabNode(
    prefab: Prefab,
    { parent, materials, position, scale, rotation, castShadows, receiveShadows, onTap }: ICreatePrefabOptions = {},
) {
    const node = instantiate(prefab);
    const meshes = node.getComponentsInChildren(MeshRenderer);

    if (materials?.length) {
        materials.forEach((material, index) => {
            meshes.forEach((mesh) => mesh.setSharedMaterial(material, index));
        });
    }

    if (position) {
        node.setPosition(position);
    }

    if (scale) {
        node.setScale(scale);
    }

    if (rotation) {
        node.setRotationFromEuler(rotation);
    }

    if (meshes.length) {
        meshes.forEach((mesh) => {
            mesh.shadowCastingMode = castShadows ? 1 : 0;
            mesh.receiveShadow = receiveShadows ? 1 : 0;
        });
    }

    if (onTap) {
        const colliders = addMeshCollider(node, { isTrigger: true });

        colliders.forEach((collider) => {
            collider.node.on(EEventName.Tap, onTap);
        });
    }

    if (parent) {
        parent.addChild(node);
    }

    return node;
}
