import { AbstractState } from '@/states/AbstractState/AbstractState';
import type { Tile } from '@/features/Tile/components/Tile';
import { HexagonMap } from '@/libs/hexagon/HexagonMap';
import { HexagonTile } from '@/libs/hexagon/HexagonTile';
import { HEX_OPTIONS } from '@/features/Hex/data/consts';
import { HexagonChunk } from '@/libs/hexagon/HexagonChunk';
import { Chunk } from '@/features/Chunk/components/Chunk';
import { action, computed, reaction } from 'vendors/mobx';
import { clamp } from '@/libs/math/clamp';
import { BUILD_PROGRESS_LIMITS, BUILD_PROGRESS_MAX } from '@/features/Building/data/consts';
import { EBuilding } from '@/features/Building/data/EBuilding';
import { Building } from '@/features/Building/components/Building';

export interface IWorldState {

}

export class WorldState extends AbstractState<IWorldState> {
    chunksMap = new HexagonMap(HexagonChunk<Chunk>, HEX_OPTIONS);
    tilesMap = new HexagonMap(HexagonTile<Tile>, HEX_OPTIONS);

    @computed
    get buildings() {
        return this.tilesMap.values.reduce<Building[]>((result, hex) => {
            const { building } = hex.self;

            if (building) {
                result.push(building);
            }

            return result;
        }, []);
    }

    @computed
    get buildQueue() {
        return this.buildings.filter(({ isConstructing }) => isConstructing);
    }

    @computed
    get buildedBuildings() {
        return this.buildings.filter(({ isConstructing }) => !isConstructing);
    }

    @computed
    get activeBuildings() {
        return this.buildedBuildings.filter(({ buildingData }) => !buildingData.isDisabled);
    }

    @computed
    get buildedCount() {
        return this.calculateBuildingsCount(this.buildedBuildings);
    }

    @computed
    get buildQueueCount() {
        return this.calculateBuildingsCount(this.buildQueue);
    }

    private calculateBuildingsCount(buildings: Building[]) {
        return buildings.reduce<Partial<Record<EBuilding, number>>>((result, building) => {
            result[building.buildingKey] = (result[building.buildingKey] || 0) + 1;

            return result;
        }, {});
    }

    disposeTurnListen = reaction(
        () => this.state.turnState.turn,
        action(() => {
            for (const building of this.buildQueue) {
                const { buildingData, constructionSpeed } = building;

                buildingData.progress = clamp(buildingData.progress + constructionSpeed, BUILD_PROGRESS_LIMITS);
            }
        })
    );

    destroy() {
        this.disposeTurnListen();
    }

    override initialize(data: IWorldState) {
        return super.initialize(data);
    }

    serialize(): IWorldState {
        return {};
    }
}
