import { Component } from 'cc';
import { autorun, makeObservable } from 'vendors/mobx';

const UNMOUNT_METHOD_REGEX = /^dispose/;

export abstract class Observer extends Component {
    protected disposeRender: (() => void) | null = null;

    __preload() {
        makeObservable(this);

        if (this.render) {
            this.disposeRender = autorun(this.render.bind(this));
        }
    }

    onDestroy() {
        const methods = [
            ...Object.getOwnPropertyNames(this),
            ...Object.getOwnPropertyNames(Object.getPrototypeOf(this)),
        ] as (keyof Observer)[];

        methods.forEach((method) => {
            if (UNMOUNT_METHOD_REGEX.test(method)) {
                const func = this[method] as any as (() => void) | undefined;

                if (func) {
                    func();
                }
            }
        });
    }

    render?(): void;
}
