import { i18n } from '@/libs/i18n/i18n';
import { AbstractState } from '@/states/AbstractState/AbstractState';
import { MONTHS, START_MONTH, START_YEAR, YEAR_LENGTH } from '@/states/TurnState/data/consts';
import { action, computed, observable } from 'vendors/mobx';

export interface ITurnState {
    turn?: number;
}

export class TurnState extends AbstractState<ITurnState> {
    @observable turn: number = 0;

    @computed
    get month() {
        return (START_MONTH + this.turn) % YEAR_LENGTH;
    }

    @computed
    get year() {
        return START_YEAR + Math.floor((START_MONTH + this.turn) / YEAR_LENGTH);
    }

    @computed
    get date() {
        return `${i18n(MONTHS[this.month])} ${this.year}`;
    }

    @action.bound
    override initialize(data: ITurnState) {
        const { turn } = data;

        if (turn !== undefined) {
            this.turn = turn;
        }

        return super.initialize(data)
    }

    serialize(): ITurnState {
        return {
            turn: this.turn,
        };
    }
}
