import type { Node } from 'cc';
import { Vec3 } from 'cc';
import { computed } from 'vendors/mobx';
import type { Tile } from '@/features/Tile/components/Tile';
import { Observer } from '@/libs/cocos/components/Observer';
import { createPrefabNode } from '@/libs/cocos/helpers/createPrefabNode';
import { ccclass } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';
import { getBuildRoute } from '@/routes/BuildRoute/helpers/getBuildRoute';

export interface ITileTestProps {
    tile: Tile;
}

const SCALE = new Vec3(0.96, 1, 0.96);

@ccclass('TileTest')
export class TileTest extends Observer {
    tile: Tile;

    testNode: Node | null;

    @computed
    get isActive() {
        const page = getBuildRoute();

        return Boolean(page && !page.buildingCoords && page.buildingDb?.tileTest?.(this.tile));
    }

    initialize({ tile }: ITileTestProps) {
        this.tile = tile;
    }

    render() {
        if (this.testNode) {
            this.testNode.active = Boolean(this.isActive);
        }
    }

    onLoad() {
        const prefab = assets.getPrefabs('hex_plane');
        const material = assets.getMaterials('ui-white-50');

        this.testNode = createPrefabNode(prefab, {
            parent: this.node,
            materials: [material],
            scale: SCALE,
            position: new Vec3(0, this.tile.groundY + 0.01, 0),
        });

        this.testNode.active = false;
    }
}
