import type { Asset } from 'cc';
import { resources } from 'cc';

type TConstructor<T = unknown> = new (...args: any[]) => T;

export async function loadResourcesDir<T extends Asset>(path: string, type: TConstructor<T> | null) {
    return new Promise<T[]>((resolve, reject) => {
        resources.loadDir(path, type, (err, data) => {
            if (err) {
                return reject(err);
            }

            return resolve(data);
        });
    });
}
