import { Vec3 } from 'cc';
import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { EBiomeSize } from '@/features/Biome/data/EBiomeSize';
import { TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { WORLD_CENTER } from '@/features/World/data/consts';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { findMinHeight } from '@/features/WorldGenerator/helpers/findMinHeight';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { clamp } from '@/libs/math/clamp';
import { isRandomChance } from '@/libs/random/isRandomChance';
import { state } from '@/states/state';

const MIN_SEA_DISTANCE = 4;

export const generateSea: TGenerator = (base, options) => {
    const { neighbors } = options;

    const distance = Vec3.distance(base.coords, WORLD_CENTER);

    if (distance < MIN_SEA_DISTANCE) {
        return base;
    }

    const minHeight = findMinHeight(neighbors);

    if (minHeight && minHeight < TILE_SEA_LEVEL) {
        return base;
    }

    const lakes = findBiomeNeighbors(EBiomeTile.Lake, options);

    if (lakes.length) {
        return base;
    }

    const seas = findBiomeNeighbors(EBiomeTile.Sea, options);
    const rivers = findBiomeNeighbors(EBiomeTile.River, options);

    if (!seas.length) {
        const { tilesMap } = state.worldState;

        const hasSea = tilesMap.values.some((hex) => hex.self.data.biomeTile?.key === EBiomeTile.Sea);

        if (hasSea) {
            return base;
        }

        const riversSize = rivers.reduce((result, { size }) => result + size, 0);

        const chance = 0.0001 * distance + riversSize * 0.0005;

        if (!isRandomChance(chance)) {
            return base;
        }

        return {
            ...base,
            height: TILE_SEA_LEVEL,
            biomeTile: {
                id: guid(),
                key: EBiomeTile.Sea,
                size: EBiomeSize.Large,
            },
        };
    }

    const [sea] = seas;

    const chance = seas.length * 0.6 + clamp(sea.diff, [-10, null]) * 0.05 + clamp(20 - sea.bordersSize, [0, null]) * 0.1;

    if (!isRandomChance(chance)) {
        return base;
    }

    return {
        ...base,
        height: TILE_SEA_LEVEL,
        biomeTile: {
            id: sea.biome.id,
            key: EBiomeTile.Sea,
            size: EBiomeSize.Large,
        },
    };
};
