import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import { isNearWorldCenter } from '@/features/WorldGenerator/helpers/center/isNearCenter';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { isRandomChance } from '@/libs/random/isRandomChance';

export const generateClay: TGenerator = (base, options) => {
    if (isNearWorldCenter(base.coords)) {
        return base;
    }

    const clays = findBiomeNeighbors(EBiomeTile.Clay, options);

    if (clays.some((clay) => clay.diff <= 0)) {
        return base;
    }

    const [clay] = clays;

    const heightDiff = TILE_SEA_LEVEL - base.height;

    if (heightDiff < 0) {
        return base;
    }

    let chance = 0.02 * heightDiff;
    if (clay?.size === 1) {
        chance = 1;
    } else if (clays.length && clays.length !== 1) {
        chance = 0.3 * clays.length;
    } else if (clay) {
        chance += 0.01 * (clay.diff || 0);
    }

    if (!isRandomChance(chance)) {
        return base;
    }

    return {
        ...base,
        biomeTile: {
            id: clay?.biome.id || guid(),
            key: EBiomeTile.Clay,
            size: clay?.biome.size || randomizeBiomeSize([0.15, 0.5, 0.3, 0.05]),
        },
    };
};
