import { ButtonAction } from '@/features/Button/components/ButtonAction';
import { ComponentPath } from '@/libs/cocos/components/ComponentPath';
import { Observer } from '@/libs/cocos/components/Observer';
import { cached } from '@/libs/decorators/cached';
import { ccclass, property } from '@/libs/decorators/cocos';
import { runInAction } from 'vendors/mobx';

@ccclass('ButtonDisabled')
export class ButtonDisabled extends Observer {
    @property(ComponentPath)
    isDisabled: ComponentPath;

    @property
    isReversed = false;

    @cached
    get button() {
        return this.node.getComponent(ButtonAction);
    }

    render() {
        let isDisabled = Boolean(this.isDisabled.exec());

        if (this.isReversed) {
            isDisabled = !isDisabled;
        }

        runInAction(() => {
            if (this.button) {
                this.button.disabled = isDisabled;
            }
        });
    }
}
