import { cached } from '@/libs/decorators/cached';
import { Hexagon } from '@/libs/hexagon/Hexagon';
import { HexagonTile } from '@/libs/hexagon/HexagonTile';

export class HexagonChunk<C> extends Hexagon<C> {
    @cached
    get tiles() {
        const center = new HexagonTile(null, this.coords.clone(), {
            hexHeight: this.hexHeight,
            hexRadius: this.hexRadius,
        });

        return [center.coords, ...center.neighbors];
    }

    @cached
    get right() {
        return this.coords.clone().add3f(3, 2, -1);
    }

    @cached
    get topRight() {
        return this.coords.clone().add3f(1, 3, 2);
    }

    @cached
    get topLeft() {
        return this.coords.clone().add3f(-2, 1, 3);
    }

    @cached
    get left() {
        return this.coords.clone().add3f(-3, -2, 1);
    }

    @cached
    get bottomLeft() {
        return this.coords.clone().add3f(-1, -3, -2);
    }

    @cached
    get bottomRight() {
        return this.coords.clone().add3f(2, -1, -3);
    }
}
