import { ccclass } from '@/libs/decorators/cocos';
import { AbstractScreen } from '@/routes/AbstractRoute/components/AbstractScreen';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { PauseRoute } from '@/routes/PauseRoute/PauseRoute';
import { router } from '@/routes/router';
import { computed } from 'vendors/mobx';

@ccclass('PauseScreen')
export class PauseScreen extends AbstractScreen<PauseRoute> {
    static template: TResourceTemplate = 'PauseScreen';

    readonly routeName = ERouteName.Pause;

    onStartClick() {}

    onContinueClick() {
        router.navigate(ERouteName.World);
    }

    @computed
    get isContinueActive() {
        return Boolean(this.route?.isIntro);
    }

    onResumeClick() {
        const isNavigated = router.navigateBack();

        if (!isNavigated) {
            this.onContinueClick();
        }
    }

    @computed
    get isResumeActive() {
        return !this.isContinueActive;
    }

    onStartOverClick() {}

    onSettingsClick() {}

    get isPaused() {
        return !this.route?.isIntro;
    }
}
