import type { IChance } from '@/libs/random/IChance';

export function randomChance<T extends Function, A extends unknown[] = []>(
    chances: IChance<T, A>[],
    args: A = [] as unknown as A,
) {
    let rand = Math.random();

    for (const chance of chances) {
        const check = chance.chance(...args);

        if (rand <= check) {
            return chance.fn;
        }

        rand -= check;
    }

    return chances[chances.length - 1].fn;
}
