import { EBiomeObject } from '@/features/Biome/data/EBiome';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { isRandomChance } from '@/libs/random/isRandomChance';
import { randomFromRange } from '@/libs/random/randomFromRange';

export const generateForest: TGenerator = (base, options) => {
    const forests = findBiomeNeighbors(EBiomeObject.Forest, options);

    if (forests.some((forest) => forest.diff <= 0)) {
        return base;
    }

    const forest = forests[0];

    const diff = forest?.diff || 0;

    const chance = 0.1 + 0.2 * diff;

    if (!isRandomChance(chance)) {
        return base;
    }

    return {
        ...base,
        biomeObject: {
            id: forest?.biome.id || guid(),
            key: EBiomeObject.Forest,
            rotate: randomFromRange([-180, 180]),
            scale: randomFromRange([0.7, 1.3], 1),
            size: forest?.biome.size || randomizeBiomeSize([0.1, 0.5, 0.35, 0.05]),
        },
    };
};
