import { ccclass, property } from '@/libs/decorators/cocos';
import { Component, Prefab } from 'cc';

@ccclass('DiTemplate')
export class DiTemplate extends Component {
    static instance: DiTemplate | null = null;

    constructor(name: string) {
        super(name);

        DiTemplate.instance = this;
    }

    @property([Prefab])
    templates: Prefab[] = [];

    getTemplate(template: TResourceTemplate) {
        return this.templates.find((prefab) => {
            return prefab.data.name === template;
        });
    }

    static get(template: TResourceTemplate): Prefab {
        if (!DiTemplate.instance) {
            throw new Error('Di template is not initalized!');
        }

        const prefab = DiTemplate.instance.getTemplate(template);

        if (!prefab) {
            throw new Error(`Di template ${template} not found!`);
        }

        return prefab;
    }
}
