import type { Vec3 } from 'cc';
import type { IChunkData } from '@/features/Chunk/types/IChunkData';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { generateWorldCenterTile } from '@/features/WorldGenerator/helpers/center/generateWorldCenterTile';
import { generateTile } from '@/features/WorldGenerator/helpers/generateTile';
import { HexagonChunk } from '@/libs/hexagon/HexagonChunk';
import { HEX_OPTIONS } from '@/features/Hex/data/consts';

export function generateWorldCenterChunk(center: Vec3): IChunkData {
    const chunk = new HexagonChunk(null, center, HEX_OPTIONS);

    const tiles: ITileData[] = [generateWorldCenterTile(center)];

    chunk.tiles.forEach((coords) => {
        if (coords.equals(center)) {
            return;
        }

        tiles.push(generateTile(coords, { neighbors: tiles }));
    });

    return {
        coords: center,
        tiles,
    };
}
