import { Vec3 } from 'cc';
import { TILE_BASE_HEIGHT, TILE_HEIGHT_MULTIPLIER, TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { Observer } from '@/libs/cocos/components/Observer';
import { ccclass } from '@/libs/decorators/cocos';
import { getBuildRoute } from '@/routes/BuildRoute/helpers/getBuildRoute';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';
import { IUi3DProps, Ui3D } from '@/features/Ui/components/Ui3D';

export interface IChunkNewButtonProps {
    hexHeight: number;
    hexRadius: number;
    onClick: () => void;
}

@ccclass('ChunkNewButton')
export class ChunkNewButton extends Observer {
    private ui?: Ui3D;

    hexHeight: number;

    hexRadius: number;

    onClick: () => void;

    initialize({ hexHeight, hexRadius, onClick }: IChunkNewButtonProps) {
        this.hexHeight = hexHeight;
        this.hexRadius = hexRadius;
        this.onClick = onClick;
    }

    onLoad() {
        this.node.setPosition(new Vec3(0, TILE_BASE_HEIGHT + TILE_HEIGHT_MULTIPLIER * TILE_SEA_LEVEL, 0));

        this.ui = factoryComponent<IUi3DProps, Ui3D>(Ui3D, this.node, {
            sprite: 'newChunkButton',
            onTap: this.onClick,
            rotation: new Vec3(90, 30, 0),
        });

        this.ui.node.active = false;
    }

    render() {
        const isActive = Boolean(getBuildRoute()?.isChunkMode);

        if (this.ui) {
            this.ui.node.active = isActive;
        }
    }
}
