import type { Asset } from 'cc';
import { resources } from 'cc';

type TConstructor<T = unknown> = new (...args: any[]) => T;

export async function loadResources<T extends Asset>(paths: string[], type: TConstructor<T> | null) {
    return new Promise<T[]>((resolve, reject) => {
        resources.load(paths, type, (err, data) => {
            if (err) {
                return reject(err);
            }

            return resolve(data);
        });
    });
}
