import { Vec3 } from 'cc';
import { action, observable } from 'vendors/mobx';
import type { IChunkData } from '@/features/Chunk/types/IChunkData';
import type { ITileProps } from '@/features/Tile/components/Tile';
import { Tile } from '@/features/Tile/components/Tile';
import { generateChunk } from '@/features/WorldGenerator/helpers/generateChunk';
import { Observer } from '@/libs/cocos/components/Observer';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';
import { ccclass } from '@/libs/decorators/cocos';
import type { HexagonChunk } from '@/libs/hexagon/HexagonChunk';
import { toVec3 } from '@/libs/math/toVec3';
import { state } from '@/states/state';
import { ChunkNewButton } from '@/features/Chunk/components/ChunkNewButton';
import type { IChunkNewButtonProps } from '@/features/Chunk/components/ChunkNewButton';

export interface IChunkProps {
    coords: Vec3;
    data: IChunkData | null;
}

@ccclass('Chunk')
export class Chunk extends Observer {
    hex: HexagonChunk<Chunk>;

    tiles: Tile[] = [];

    @observable data: IChunkData | null;

    initialize({ coords, data }: IChunkProps) {
        const { chunksMap } = state.worldState;

        this.hex = chunksMap.add(coords, this);
        this.data = data;
    }

    draw() {
        if (!this.data) {
            factoryComponent<IChunkNewButtonProps, ChunkNewButton>(ChunkNewButton, this.node, {
                hexHeight: this.hex.hexHeight,
                hexRadius: this.hex.hexRadius,
                onClick: this.generate,
            });

            return;
        }

        this.tiles = this.data.tiles.map((tileData) =>
            factoryComponent<ITileProps, Tile>(Tile, this.node, {
                coords: toVec3(tileData.coords),
                chunk: this,
                data: tileData,
            }),
        );

        this.hex.neighbors.forEach((coords) => {
            const { chunksMap } = state.worldState;

            if (!chunksMap.get(coords) && this.node.parent) {
                const chunk = factoryComponent<IChunkProps, Chunk>(Chunk, this.node.parent, { coords, data: null });

                chunk.draw();
            }
        });
    }

    @action.bound
    generate() {
        if (!this.data) {
            this.node.destroyAllChildren();

            this.data = generateChunk(this.hex.coords);

            this.draw();
        }
    }

    onLoad() {
        this.node.setWorldPosition(new Vec3(this.hex.positionX, 0, this.hex.positionZ));
    }
}
