import { sys } from 'cc';
import type { Material } from 'cc';

// Copy of material.setProperty, but without a warning
function setMaterialProperty<T>(this: Material, name: string, val: T | T[], passIdx?: number) {
    let success = false;
    if (passIdx === undefined) {
        // try set property for all applicable passes
        const passes = this._passes;
        const len = passes.length;
        for (let i = 0; i < len; i++) {
            const pass = passes[i];
            if ((this as any)._uploadProperty(pass, name, val)) {
                (this as any)._props[pass.propertyIndex][name] = val;
                success = true;
            }
        }
    } else {
        if (passIdx >= this._passes.length) {
            console.warn(`illegal pass index: ${passIdx}.`);
            return;
        }
        const pass = this._passes[passIdx];
        if ((this as any)._uploadProperty(pass, name, val)) {
            (this as any)._props[pass.propertyIndex][name] = val;
            success = true;
        }
    }
    return success;
}

export function prepareMaterial(material: Material) {
    const warn = console.warn;

    try {
        console.warn = () => {};

        material.setProperty('u_globalDirection', sys.isNative ? -1.0 : 1.0);
    } catch (err) {
        warn(err);
    } finally {
        console.warn = warn;
    }

    return material;
}
