import { IResourceProps, ResourceComponent } from '@/features/Resource/components/ResourceComponent';
import { TemplateComponent } from '@/libs/cocos/components/TemplateComponent';
import { updateNodeLayout } from '@/libs/cocos/helpers/updateNodeLayout';
import { ccclass, property } from '@/libs/decorators/cocos';
import { Label, Node, Sprite } from 'cc';
import { computed } from 'vendors/mobx';

export interface IResourceCardProps extends IResourceProps {

}

@ccclass('ResourceCard')
export class ResourceCard extends ResourceComponent implements TemplateComponent {
    static template: TResourceTemplate = 'ResourceCard';

    @computed
    get color() {
        if (this.count < 0) {
            return ResourceComponent.NEGATIVE_COLOR;
        }

        if (this.count > 0) {
            return ResourceComponent.NEUTRAL_COLOR;
        }

        return ResourceComponent.POSITIVE_COLOR;
    }

    render() {
        super.render();

        const labelBgSprite = this.label.node.parent?.getComponent(Sprite);

        if (labelBgSprite) {
            labelBgSprite.color = this.color;
        }

        updateNodeLayout(this);
    }
}
