import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { tileHasBiomeTiles } from '@/features/Tile/helpers/tileHasBiomeTiles';
import { Tile } from '@/features/Tile/components/Tile';

export function tileNotWater(tile: Tile) {
    return !tile.data.biomeTile || !tileHasBiomeTiles(tile, [EBiomeTile.River, EBiomeTile.Sea, EBiomeTile.Lake]);
}
