export enum EMoveType {
    Default = 1, // movement
    TwoFinger = 2, // when two fingers are on screen (zoom + rotate)
    DoubleTouch = 3, // when double tapped the screen (zoom + rotate)
    Impulse = 4, // impulse after movement
    Rotate = 5, // rotate to preview
    Navigate = 6, // camera navigation
}
