import { CCClass, Enum, Node } from 'cc';
import { property, ccclass } from '@/libs/decorators/cocos';
import { getAllPropertyDescriptors } from '@/libs/helpers/getAllPropertyDescriptors';

@ccclass('ComponentPath')
export class ComponentPath {
    @property(Node)
    target: Node;

    get targetComponents() {
        if (!this.target) {
            return [];
        }

        const components = this.target.components;

        return components.map((component) => {
            const name = component.constructor.name;

            return { name, value: name };
        });
    }

    @property({
        type: Enum({}),
        visible(this: ComponentPath) {
            const visible = Boolean(this.target);

            if (visible) {
                CCClass.Attr.setClassAttr(this, 'component', 'enumList', this.targetComponents);
            }

            return visible;
        },
    })
    public component: string = '';

    get realComponent() {
        if (this.component) {
            return this.target.getComponent(this.component);
        }

        return null;
    }

    get componentProperties() {
        const component = this.realComponent;

        if (!component) {
            return [];
        }

        const properties = getAllPropertyDescriptors(component, 1);

        return Object.entries(properties)
            .filter(([, desc]) => {
                return Boolean(desc.get);
            })
            .map(([name]) => {
                return { name, value: name };
            });
    }

    @property({
        type: Enum({}),
        visible(this: ComponentPath) {
            const visible = Boolean(this.realComponent);

            if (visible) {
                CCClass.Attr.setClassAttr(this, 'property', 'enumList', this.componentProperties);
            }

            return visible;
        },
    })
    public property: string = '';

    exec<T>(): T | null {
        const { realComponent: component, property } = this;

        if (component && property in component) {
            return (component as any)[property] as T;
        }

        console.error(`Cannot find path ${property} in ${this.component}`);

        return null;
    }
}
