import { Vec3 } from 'cc';
import { TILE_HEIGHT_LIMITS, TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { WORLD_CENTER } from '@/features/World/data/consts';
import { findWaterNeighbors } from '@/features/WorldGenerator/helpers/biomes/findWaterNeighbors';
import type { IGeneratorOptions } from '@/features/WorldGenerator/types/IGeneratorOptions';
import { clamp } from '@/libs/math/clamp';
import type { IChance } from '@/libs/random/IChance';
import { randomChance } from '@/libs/random/randomChance';
import { randomFromArray } from '@/libs/random/randomFromArray';

const CHANCES: IChance<(h: number) => number, [number]>[] = [
    {
        name: 'higher',
        chance: (height) => {
            const diff = TILE_SEA_LEVEL - height;
            return 0.2 + 0.03 * diff;
        },
        fn: (height) => height + 1,
    },
    {
        name: 'lower',
        chance: (height) => {
            const diff = height - TILE_SEA_LEVEL;
            return 0.2 + 0.03 * diff;
        },
        fn: (height) => height - 1,
    },
    {
        name: 'same',
        chance: () => 1,
        fn: (height) => height,
    },
];

function getMainNeighbor(neighbors: ITileData[]) {
    const center = neighbors.find(({ coords }) => Vec3.equals(WORLD_CENTER, coords));

    if (center) {
        return center;
    }

    return randomFromArray(neighbors);
}

export function generateTileHeight(options: IGeneratorOptions) {
    const { neighbors } = options;

    const waters = findWaterNeighbors(options);

    const main = getMainNeighbor(neighbors);
    const height = main?.height ?? TILE_SEA_LEVEL;

    const fn = randomChance(CHANCES, [height]);

    const resultHeight = clamp(fn(height), TILE_HEIGHT_LIMITS);

    if (waters.length) {
        const waterHeight = waters.reduce<number | null>((result, water) => {
            if (!result || water.tile.height > result) {
                return water.tile.height;
            }

            return result;
        }, null);

        if (waterHeight && resultHeight < waterHeight) {
            return waterHeight;
        }
    }

    return resultHeight;
}
