import { DiTemplate } from '@/features/Di/components/DiTemplate';
import { TemplateComponent } from '@/libs/cocos/components/TemplateComponent';
import { TComponentClass } from '@/libs/cocos/types/TComponentClass';
import { Component, instantiate, Node } from 'cc';

interface IComponent<P> extends Component {
    initialize?(props: P): void;
}

export function factoryTemplateComponent<P, T extends TemplateComponent & IComponent<P>>(Comp: TComponentClass<T> & { template: TResourceTemplate }, parent: Node, props?: P) {
    const template = DiTemplate.get(Comp.template);
    const node = instantiate(template);
    const component = node.getComponent(Comp);

    if (!component) {
        throw new Error(`Cannot find ${component} in template ${Comp.template}!`);
    }

    if (props) {
        (component as any).initialize?.(props);
    }

    node.layer = parent.layer;

    parent.addChild(node);

    return component;
}
