import { TComponentClass } from '@/libs/cocos/types/TComponentClass';
import { AbstractScreen } from '@/routes/AbstractRoute/components/AbstractScreen';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { makeObservable } from 'vendors/mobx';

export abstract class AbstractRoute<T = {}> {
    hasUiOverlay = false;
    hasUiHeader = true;

    abstract readonly name: ERouteName;
    abstract readonly screen: TComponentClass<AbstractScreen<AbstractRoute>> & { template: TResourceTemplate };

    initialize(options: T) {
        Object.assign(this, options);
        makeObservable(this);

        return this;
    }

    abstract serialize(): T;

    destroy() {
        // no-op
    }
}
