import { UiOpacityAnimate } from '@/features/UiAnimation/components/UiOpacityAnimate';
import { Observer } from '@/libs/cocos/components/Observer';
import { TemplateComponent } from '@/libs/cocos/components/TemplateComponent';
import { AbstractRoute } from '@/routes/AbstractRoute/AbstractRoute';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { router } from '@/routes/router';
import { UIOpacity } from 'cc';
import { computed } from 'vendors/mobx';

export abstract class AbstractScreen<T extends AbstractRoute> extends Observer implements TemplateComponent {
    abstract readonly routeName: ERouteName;

    @computed
    get route() {
        return router.getRoute<T>(this.routeName);
    }

    initialize() {
        const opacity = this.node.addComponent(UIOpacity);
        opacity.opacity = 0;

        const animate = this.node.addComponent(UiOpacityAnimate);
        animate.duration = 0.1;
        animate.easing = 'cubicInOut';
    }
}
