import { DiWorld } from '@/features/Di/components/DiWorld';
import { Observer } from '@/libs/cocos/components/Observer';
import { cached } from '@/libs/decorators/cached';
import { ccclass, property } from '@/libs/decorators/cocos';
import { state } from '@/states/state';
import { Widget, Node, tween, Tween, Label } from 'cc';
import { computed } from 'vendors/mobx';

const ROTATE_DURATION = 1;

@ccclass('ButtonTurn')
export class ButtonTurn extends Observer {
    @property(Node)
    outer: Node;

    @property(Node)
    inner: Node;

    @property(Node)
    label: Node;

    @cached
    get startTurn() {
        return state.turnState.turn;
    }

    @computed
    get angle() {
        return -60 * (state.turnState.turn - this.startTurn) + this.fullTurn * 360;
    }

    @cached
    get labelComponent() {
        return this.label.getComponent(Label);
    }

    fullTurn = 0;

    turnAnimation: Tween<Node> | null = null;

    onLoad() {
        const { worldUi } = DiWorld;

        const wrapperWidget = worldUi.contentNode.getComponent(Widget);
        const widget = this.node.getComponent(Widget);

        if (widget && wrapperWidget) {
            widget.bottom -= wrapperWidget.bottom;
            widget.updateAlignment();
        }

        if (this.labelComponent) {
            this.labelComponent.string = state.turnState.date;
        }
    }

    render() {
        if (this.turnAnimation) {
            this.turnAnimation.stop();
            this.turnAnimation = null;
        }

        this.turnAnimation = tween(this.outer)
            .to(ROTATE_DURATION, { angle: this.angle }, { easing: 'cubicInOut' })
            .call(() => {
                if (this.outer.angle <= -360) {
                    this.fullTurn += Math.floor(Math.abs(this.outer.angle / 360));
                    this.outer.angle = this.outer.angle % 360;
                }

                if (this.labelComponent) {
                    this.labelComponent.string = state.turnState.date;
                }
            })
            .start();
    }
}
