import { assets } from '@/features/Assets/data/assets';
import { EResource } from '@/features/Resource/data/EResource';
import { ResourcesDB } from '@/features/Resource/data/ResourcesDB';
import { UiLabel } from '@/features/Ui/components/UiLabel';
import { Observer } from '@/libs/cocos/components/Observer';
import { toCocosEnum } from '@/libs/cocos/helpers/toCocosEnum';
import { property } from '@/libs/decorators/cocos';
import { Color, Label, Sprite } from 'cc';
import { action, computed, observable } from 'vendors/mobx';

export interface IResourceProps {
    resource: EResource;
    count: number;
}

export class ResourceComponent extends Observer {
    static NEGATIVE_COLOR = new Color(219, 84, 97);
    static POSITIVE_COLOR = new Color(0, 177, 134);
    static NEUTRAL_COLOR = new Color(224, 202, 60);

    @property(Label)
    label: Label;

    @property(UiLabel)
    title?: UiLabel;

    @property(Sprite)
    sprite: Sprite;

    @property({ type: toCocosEnum(EResource) })
    @observable resource: string = '';

    @property
    @observable count: number = 0;

    get resourceKey(): EResource | null {
        if (this.resource) {
            return this.resource as EResource;
        }

        return null;
    }

    @computed
    get spriteFrame() {
        if (!this.resourceKey) {
            return null;
        }

        const resourceDb = ResourcesDB[this.resourceKey];

        return assets.getSprites(resourceDb.icon);
    }

    @computed
    get str() {
        if (this.count > 0) {
            return `+${this.count}`;
        }

        return `${this.count}`;
    }

    @action.bound
    initialize({ resource, count }: IResourceProps) {
        this.resource = resource;
        this.count = count;
    }

    @computed
    get titleStr() {
        if (!this.resourceKey) {
            return '';
        }

        return ResourcesDB[this.resourceKey].title;
    }

    render() {
        this.sprite.spriteFrame = this.spriteFrame;
        this.label.string = this.str;

        if (this.title) {
            this.title.string = this.titleStr;
        }
    }
}
