import type { IChunkProps } from '@/features/Chunk/components/Chunk';
import { Chunk } from '@/features/Chunk/components/Chunk';
import { WORLD_CENTER } from '@/features/World/data/consts';
import { generateWorldCenterChunk } from '@/features/WorldGenerator/helpers/center/generateWorldCenterChunk';
import { Observer } from '@/libs/cocos/components/Observer';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';
import { ccclass } from '@/libs/decorators/cocos';

@ccclass('World')
export class World extends Observer {
    onLoad() {
        const { node } = this;

        const centerChunk = generateWorldCenterChunk(WORLD_CENTER.clone());

        const chunk = factoryComponent<IChunkProps, Chunk>(Chunk, node, {
            coords: WORLD_CENTER.clone(),
            data: centerChunk,
        });

        chunk.draw();
    }
}
