import type { Node } from 'cc';
import { Vec3 } from 'cc';
import { computed } from 'vendors/mobx';
import type { IBiomeProps } from '@/features/Biome/components/Biome';
import { Biome } from '@/features/Biome/components/Biome';
import { biomeObjectPrefabs } from '@/features/Biome/data/biomeObjectsPrefabs';
import type { IBiomeObjectData } from '@/features/Biome/types/IBiomeData';
import { createPrefabNode } from '@/libs/cocos/helpers/createPrefabNode';
import { ccclass } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';

export type TBiomeObjectProps = IBiomeProps<IBiomeObjectData>;

@ccclass('BiomeObject')
export class BiomeObject extends Biome<IBiomeObjectData> {
    @computed
    get prefab() {
        return biomeObjectPrefabs[this.data.key]?.(this.data);
    }

    protected createRenderNode(): Node | null {
        if (!this.prefab) {
            return null;
        }

        const prefab = assets.getPrefabs(this.prefab);

        return createPrefabNode(prefab, {
            parent: this.node,
            rotation: this.data.rotate ? new Vec3(0, this.data.rotate, 0) : undefined,
            scale: this.data.scale ? new Vec3(1, this.data.scale, 1) : undefined,
            castShadows: true,
            receiveShadows: true,
            onTap: this.onTap,
        });
    }

    onLoad() {
        this.node.setPosition(new Vec3(0, this.tile.groundY, 0));
    }
}
