import type { Vec3 } from 'cc';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { generateBiomeObject } from '@/features/WorldGenerator/helpers/biomes/generateBiomeObject';
import { generateBiomeTile } from '@/features/WorldGenerator/helpers/biomes/generateBiomeTile';
import { generateTileBaseHeight } from '@/features/WorldGenerator/helpers/generateTileBaseHeight';
import { generateTileHeight } from '@/features/WorldGenerator/helpers/generateTileHeight';
import type { IGeneratorOptions } from '@/features/WorldGenerator/types/IGeneratorOptions';

export function generateTile(coords: Vec3, options: IGeneratorOptions): ITileData {
    const height = generateTileHeight(options);
    const baseHeight = generateTileBaseHeight();

    let base: ITileData = { coords, height, baseHeight };

    base = generateBiomeTile(base, options);
    base = generateBiomeObject(base, options);

    return base;
}
