import { Observer } from '@/libs/cocos/components/Observer';
import { setNodeLayer } from '@/libs/cocos/helpers/setNodeLayer';
import { cached } from '@/libs/decorators/cached';
import { ccclass, property } from '@/libs/decorators/cocos';
import { Semaphore } from '@/libs/semaphore/Semaphore';
import { Camera, director, MeshRenderer, Node, SpriteFrame, sys, Texture2D, Vec3 } from 'cc';

@ccclass('Photobooth')
export class Photobooth extends Observer {
    @property(Node)
    root: Node;

    @property(Camera)
    camera: Camera;

    @cached
    get rt() {
        return this.camera.targetTexture;
    }

    semaphore = new Semaphore();

    updateCallback: (() => void) | null = null;

    async waitForUpdate() {
        await new Promise<void>((resolve) => {
            this.updateCallback = resolve;
        });

        this.updateCallback = null;
    }

    async capture(node: Node, { scale }: { scale?: number } = {}) {
        return this.semaphore.execute(async () => {
            this.root.destroyAllChildren();

            this.camera.node.parent?.setPosition(new Vec3(0, 0, 0));

            setNodeLayer(node, this.root.layer);

            if (scale) {
                node.setScale(new Vec3(scale, scale, scale));
            }

            this.root.addChild(node);

            const bounds = node.getComponentInChildren(MeshRenderer)?.model?.worldBounds;

            if (bounds) {
                this.camera.node.parent?.setPosition(new Vec3(0, bounds.center.y + bounds.halfExtents.y, 0));
            }

            await this.waitForUpdate();

            if (!this.rt) {
                return null;
            }

            const pbuffer = this.rt.readPixels();

            if (!pbuffer) {
                return null;
            }

            const texture = new Texture2D();

            texture.reset({
                width: this.rt.width,
                height: this.rt.height,
                format: this.rt.getPixelFormat(),
            });

            texture.uploadData(pbuffer);

            const spriteFrame = new SpriteFrame();

            spriteFrame.texture = texture;

            spriteFrame.flipUVY = !sys.isNative;

            return spriteFrame;
        });
    }

    update() {
        this.updateCallback?.();
    }

    static instance: Photobooth | null = null;

    static factory() {
        if (!this.instance || !this.instance.node.activeInHierarchy) {
            const scene = director.getScene();

            this.instance = scene?.getComponentInChildren(Photobooth) ?? null;
        }

        return this.instance;
    }
}
