import { Observer } from '@/libs/cocos/components/Observer';
import { ccclass, property } from '@/libs/decorators/cocos';
import { tween, TweenEasing, Vec3 } from 'cc';

@ccclass('UiPositionAnimate')
export class UiPositionAnimate extends Observer {
    @property(Vec3)
    by: Vec3 = new Vec3(0, 0, 0);

    @property
    duration = 1;

    @property
    delay = 0;

    @property
    easing: TweenEasing = 'cubicIn';

    onLoad() {
        const pos = this.node.position.clone();

        this.node.setPosition(pos.clone().add(this.by));

        tween(this.node).to(this.duration, { position: pos }, { easing: this.easing }).start();
    }
}
