import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { generateForest } from '@/features/WorldGenerator/helpers/biomes/generateForest';
import { generateForestRocks } from '@/features/WorldGenerator/helpers/biomes/generateForestRocks';
import { generateHill } from '@/features/WorldGenerator/helpers/biomes/generateHill';
import { generateMountain } from '@/features/WorldGenerator/helpers/biomes/generateMountain';
import { generateRocks } from '@/features/WorldGenerator/helpers/biomes/generateRocks';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';

export const generateBiomeObject: TGenerator = (base, options) => {
    let result = base;

    if (!result.height) {
        return result;
    }

    if (result.biomeTile) {
        if (result.biomeTile.key === EBiomeTile.Sand) {
            result = generateMountain(result, options);

            if (!result.biomeObject) {
                result = generateRocks(result, options);
            }

            return result;
        }

        return result;
    }

    result = generateMountain(result, options);

    if (!result.biomeObject) {
        result = generateForest(result, options);
    }

    if (!result.biomeObject) {
        result = generateRocks(result, options);
    }

    if (!result.biomeObject) {
        result = generateForestRocks(result, options);
    }

    if (!result.biomeObject) {
        result = generateHill(result, options);
    }

    return result;
};
