import { ccclass } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';
import { computed, runInAction } from 'vendors/mobx';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';
import { IUi3DProps, Ui3D } from '@/features/Ui/components/Ui3D';
import { Vec3 } from 'cc';
import { bound } from '@/libs/decorators/bound';
import { ITapEvent } from '@/libs/cocos/types/ITapEvent';
import { BUILD_PROGRESS_MAX } from '@/features/Building/data/consts';
import { cached } from '@/libs/decorators/cached';
import { BuildingComponent, IBuildingComponentProps } from '@/features/Building/components/BuildingComponent';

export interface IBuildingPreviewProps extends IBuildingComponentProps {}

@ccclass('BuildingPreview')
export class BuildingPreview extends BuildingComponent {
    protected rotateButton: Ui3D;

    @computed
    get buildingData() {
        return {
            ...this.data,
            progress: BUILD_PROGRESS_MAX
        };
    }

    @cached
    get materials() {
        const material = assets.getMaterials('white');

        return [material];
    }

    @bound
    onBuildingTap(event: ITapEvent) {
        this.rotateButton.onClick(event);
    }

    render() {
        super.render();

        if (!this.rotateButton) {
            this.rotateButton = factoryComponent<IUi3DProps, Ui3D>(Ui3D, this.node, {
                sprite: 'BubbleRotate',
                onTap: this.onBuildingTap,
            });
        }

        runInAction(() => {
            this.rotateButton.position = new Vec3(0, this.buildingHeight, 0);
        });
    }
}
