import { BuildingsDB } from '@/features/Building/data/BuildingsDB';
import { EBuilding } from '@/features/Building/data/EBuilding';
import { PhotoboothIcon } from '@/features/Photobooth/components/PhotoboothIcon';
import { UiLabel } from '@/features/Ui/components/UiLabel';
import { Observer } from '@/libs/cocos/components/Observer';
import { ccclass, property } from '@/libs/decorators/cocos';
import { toCocosEnum } from '@/libs/cocos/helpers/toCocosEnum';
import { assets } from '@/features/Assets/data/assets';
import { Sprite, Node, Label, Widget, Layout } from 'cc';
import { action, computed, observable } from 'vendors/mobx';
import { TemplateComponent } from '@/libs/cocos/components/TemplateComponent';
import { state } from '@/states/state';
import { factoryTemplateComponent } from '@/libs/cocos/helpers/factoryTemplateComponent';
import { IResourceAmountProps, ResourceAmount } from '@/features/Resource/components/ResourceAmount';
import { EResource } from '@/features/Resource/data/EResource';
import { updateNodeLayout } from '@/libs/cocos/helpers/updateNodeLayout';

export interface IBuildCardProps {
    building: EBuilding;
    onClick?: (building: EBuilding) => void;
}

@ccclass('BuildCard')
export class BuildCard extends Observer implements TemplateComponent {
    static template: TResourceTemplate = 'BuildCard';

    @property({ type: toCocosEnum(EBuilding) })
    building: string = '';

    @property(Sprite)
    coloredBg: Sprite;

    @property(PhotoboothIcon)
    icon: PhotoboothIcon;

    @property(UiLabel)
    title: UiLabel;

    @property(Node)
    border: Node;

    @property(Label)
    allCount: Label;

    @property(Label)
    count: Label;

    @property(Label)
    addCount: Label;

    @property(Node)
    resources: Node;

    @property
    @observable
    selected = false;

    onClick?: (building: EBuilding) => void;

    @computed
    get buildedCount() {
        return (this.buildingKey && state.worldState.buildedCount[this.buildingKey]) || 0;
    }

    @computed
    get buildQueueCount() {
        return (this.buildingKey && state.worldState.buildQueueCount[this.buildingKey]) || 0;
    }

    @computed
    get priceColor() {
        if (this.buildingData?.buildOptions) {
            const { resources } = state.resourcesState;
            const { price } = this.buildingData.buildOptions;

            const priceEntries = Object.entries(price) as [EResource, number][];

            let affordable = 0;

            for (const [resource, value] of priceEntries) {
                const currentResource = resources[resource];

                if (currentResource === undefined) {
                    return ResourceAmount.NEGATIVE_COLOR;
                }

                if (currentResource + value >= 0) {
                    affordable++;
                }
            }

            if (!affordable) {
                return ResourceAmount.NEGATIVE_COLOR;
            }

            if (affordable < priceEntries.length) {
                return ResourceAmount.NEGATIVE_COLOR;
            }
        }

        return ResourceAmount.POSITIVE_COLOR;
    }

    get buildingKey(): EBuilding | null {
        if (this.building) {
            return this.building as EBuilding;
        }

        return null;
    }

    @computed
    get buildingData() {
        return this.buildingKey && BuildingsDB[this.buildingKey];
    }

    onLoad() {
        const data = this.buildingData;

        if (!data || !data.buildOptions) {
            return;
        }

        const prefab = assets.getPrefabs(data.prefab);

        if (data.buildOptions.iconScale) {
            this.icon.scale = data.buildOptions.iconScale;
        }

        this.icon.shootPrefab = prefab;

        this.title.string = data.title;

        this.coloredBg.color = data.buildOptions.iconColor;

        const resources = Object.keys(data.buildOptions.price) as EResource[];

        for (const resource of resources) {
            const count = data.buildOptions.price[resource]!;

            factoryTemplateComponent<IResourceAmountProps, ResourceAmount>(ResourceAmount, this.resources, {
                resource,
                count,
            });
        }

        const resourcesBg = this.resources.getComponent(Sprite);

        if (resourcesBg) {
            resourcesBg.color = this.priceColor;
        }
    }

    render() {
        this.border.active = this.selected;

        this.count.string = this.buildedCount ? `${this.buildedCount}` : '';
        this.addCount.string = this.buildQueueCount ? `+${this.buildQueueCount}` : '';
        this.allCount.string = this.count.string + this.addCount.string;

        this.allCount.node.active = Boolean(this.allCount.string);

        updateNodeLayout(this);
    }

    @action.bound
    initialize({ building, onClick }: IBuildCardProps) {
        this.building = building;
        this.onClick = onClick;
    }

    onCardClick() {
        const key = this.buildingKey;

        if (key) {
            this.onClick?.(this.buildingKey);
        }
    }
}
