import { biomeSizeMaps } from '@/features/Biome/data/biomeSizeMaps';
import type { EBiomeObject, EBiome } from '@/features/Biome/data/EBiome';
import { BIOME_OBJECT_KEYS } from '@/features/Biome/data/EBiome';
import { getBiomeActualSize } from '@/features/Biome/helpers/getBiomeActualSize';
import type { IBiomeBaseData } from '@/features/Biome/types/IBiomeData';
import type { IGeneratorOptions } from '@/features/WorldGenerator/types/IGeneratorOptions';
import type { INeigborBiome } from '@/features/WorldGenerator/types/INeigborBiome';

export function findBiomeNeighbors<T extends IBiomeBaseData>(
    biomeKey: EBiome,
    { neighbors }: IGeneratorOptions,
): INeigborBiome<T>[] {
    const sizeMap = biomeSizeMaps[biomeKey];
    const isObject = BIOME_OBJECT_KEYS.includes(biomeKey as EBiomeObject);

    const biomeNeighbors = neighbors.reduce<INeigborBiome<T>[]>((result, tile) => {
        const biome = isObject ? tile.biomeObject : tile.biomeTile;

        if (biome?.key === biomeKey) {
            const { size, bordersSize } = getBiomeActualSize(tile.coords, biome.id, biomeKey, neighbors);

            result.push({
                biome: biome as unknown as T,
                size,
                bordersSize,
                diff: (sizeMap?.[biome.size] || 0) - size,
                tile,
            });
        }

        return result;
    }, []);

    return biomeNeighbors.sort((a, b) => b.diff - a.diff);
}
