import { EBiomeObject } from '@/features/Biome/data/EBiome';
import { TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import { isNearWorldCenter } from '@/features/WorldGenerator/helpers/center/isNearCenter';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { isRandomChance } from '@/libs/random/isRandomChance';
import { randomFromRange } from '@/libs/random/randomFromRange';

export const generateNewMountain: TGenerator = (base) => ({
    ...base,
    biomeObject: {
        id: guid(),
        key: EBiomeObject.Mountain,
        rotate: randomFromRange([-180, 180]),
        scale: randomFromRange([1, 2], 1),
        size: randomizeBiomeSize([0.6, 0.25, 0.2, 0.05]),
    },
});

export const generateMountain: TGenerator = (base, options) => {
    if (isNearWorldCenter(base.coords)) {
        return base;
    }

    const mountains = findBiomeNeighbors(EBiomeObject.Mountain, options);

    if (mountains.some((mountain) => mountain.diff <= 0)) {
        return base;
    }

    const mountain = mountains[0];

    const diff = mountain?.diff || 0;

    const height = base.height - TILE_SEA_LEVEL;

    const chance = 0.015 * height + 0.2 * diff;

    if (!isRandomChance(chance)) {
        return base;
    }

    base = generateMountain(base, options);

    if (mountain && base.biomeObject) {
        base.biomeObject.size = mountain.biome.size;
        base.biomeObject.id = mountain.biome.id;
    }

    return base;
};
