import { ComponentPath } from '@/libs/cocos/components/ComponentPath';
import { Observer } from '@/libs/cocos/components/Observer';
import { ccclass, property } from '@/libs/decorators/cocos';

@ccclass('UiActive')
export class UiActive extends Observer {
    @property(ComponentPath)
    isActive: ComponentPath;

    @property
    isReversed = false;

    render() {
        let isActive = Boolean(this.isActive.exec());

        if (this.isReversed) {
            isActive = !isActive;
        }

        this.node.active = isActive;
    }
}
