import { BUTTON_TRANSITION_SCALE, BUTTON_TRANSITION_TIME } from '@/features/Button/data/consts';
import { DiWorld } from '@/features/Di/components/DiWorld';
import { Observer } from '@/libs/cocos/components/Observer';
import { EEventName } from '@/libs/cocos/data/EEventName';
import { ELayer } from '@/libs/cocos/data/ELayer';
import { ITapEvent } from '@/libs/cocos/types/ITapEvent';
import { bound } from '@/libs/decorators/bound';
import { ccclass, property } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';
import { BoxCollider, SpriteRenderer, tween, Vec3, Vec2 } from 'cc';
import { action, autorun, computed, IReactionDisposer, observable, reaction } from 'vendors/mobx';

export interface IUi3DProps {
    sprite: TResourceSprite;
    position?: Vec3;
    rotation?: Vec3;
    onTap?: (event: ITapEvent) => void;
}

@ccclass('Ui3D')
export class Ui3D extends Observer {
    @property @observable sprite: TResourceSprite;

    @property(Vec3) @observable.ref position?: Vec3;
    @property(Vec3) @observable.ref rotation?: Vec3;

    onTap?: (event: ITapEvent) => void;

    @computed
    protected get spriteFrame() {
        return assets.getSprites(this.sprite);
    }

    spriteSize?: Vec2;

    protected getSpriteSize(): Vec2 {
        const result = new Vec2();

        const bounds = this.spriteRenderer.model?.worldBounds;

        if (bounds) {
            const minVec = new Vec3();
            const maxVec = new Vec3();

            bounds.getBoundary(minVec, maxVec);

            Vec2.subtract(result, new Vec2(maxVec.x, maxVec.y), new Vec2(minVec.x, minVec.y));
        }

        return result;
    }

    protected spriteRenderer: SpriteRenderer;
    protected collider: BoxCollider;
    protected disposeTweenScale: (() => void) | null = null;

    @action.bound
    initialize({ sprite, onTap, position, rotation }: IUi3DProps) {
        this.sprite = sprite;
        this.onTap = onTap;
        this.position = position;
        this.rotation = rotation;
    }

    @bound
    onClick(event: ITapEvent) {
        this.disposeTweenScale?.();

        const { scale } = this.node;

        const tweenScale = tween(this.node)
            .to(BUTTON_TRANSITION_TIME, { scale: scale.clone().multiplyScalar(BUTTON_TRANSITION_SCALE) })
            .to(BUTTON_TRANSITION_TIME, { scale: scale.clone() })
            .call(() => {
                this.disposeTweenScale = null;
            })
            .start();

        this.disposeTweenScale = () => {
            tweenScale.stop();

            this.disposeTweenScale = null;

            this.node.setScale(scale.clone());
        };

        this.onTap?.(event);
    }

    disposeSpriteChange?: IReactionDisposer;
    disposeCameraListen?: IReactionDisposer;

    onLoad() {
        const spriteMaterial = assets.getMaterials('ui-sprite');

        this.spriteRenderer = this.node.addComponent(SpriteRenderer);

        this.spriteRenderer.setSharedMaterial(spriteMaterial, 0);
        this.spriteRenderer.spriteFrame = this.spriteFrame;
        this.spriteSize = this.getSpriteSize();

        this.collider = this.node.addComponent(BoxCollider);
        this.collider.size = new Vec3(this.spriteSize.x, this.spriteSize.y, 0.01);
        this.collider.isTrigger = true;

        this.node.layer = ELayer.UI_3D;

        this.node.on(EEventName.Tap, this.onClick);

        this.disposeSpriteChange = reaction(() => this.spriteFrame, (spriteFrame) => {
            const rotation = this.node.eulerAngles.clone();

            this.node.setRotationFromEuler(new Vec3(0, 0, 0));

            this.spriteRenderer.spriteFrame = spriteFrame;
            this.spriteSize = this.getSpriteSize();
            this.collider.size = new Vec3(this.spriteSize.x, this.spriteSize.y, 0.01);

            this.node.setRotationFromEuler(rotation);
        });

        this.disposeCameraListen = autorun(() => {
            if (this.rotation) {
                return;
            }

            const { worldCamera: camera } = DiWorld;

            this.node.setRotationFromEuler(new Vec3(camera.cameraRotation?.x || 0, camera.targetRotation?.y || 0, camera.targetRotation?.z || 0));
        });
    }

    render() {
        if (this.position) {
            this.node.setPosition(
                !this.rotation ? this.position.clone().add3f(0, (this.spriteSize?.y || 0) * 0.75, 0) : this.position
            );
        }

        if (this.rotation) {
            this.node.setRotationFromEuler(this.rotation);
        }
    }
}
