import type { IVec3Like } from 'cc';
import { Vec3 } from 'cc';
import { EBiomeTile } from '@/features/Biome/data/EBiome';
import { waterTiles } from '@/features/Biome/data/waterTiles';
import { TILE_HEIGHT_LIMITS } from '@/features/Tile/data/consts';
import { generateClay } from '@/features/WorldGenerator/helpers/biomes/generateClay';
import { generateLake } from '@/features/WorldGenerator/helpers/biomes/generateLake';
import { generateRiver } from '@/features/WorldGenerator/helpers/biomes/generateRiver';
import { generateSand } from '@/features/WorldGenerator/helpers/biomes/generateSand';
import { generateSea } from '@/features/WorldGenerator/helpers/biomes/generateSea';
import { findMinHeight } from '@/features/WorldGenerator/helpers/findMinHeight';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { clamp } from '@/libs/math/clamp';

export const generateBiomeTile: TGenerator = (base, options) => {
    let result = base;

    if (!result.height) {
        return result;
    }

    result = generateSea(result, options);

    if (!result.biomeTile) {
        result = generateRiver(result, options);

        if (result.biomeTile?.key === EBiomeTile.River) {
            for (const tile of options.chunkTiles || []) {
                if (!tile.biomeTile || tile.biomeTile.key !== EBiomeTile.River) {
                    // If we already generated end or start of rivers
                    const target: IVec3Like = result.biomeTile.data?.from;

                    if (target && Vec3.equals(target, tile.coords)) {
                        if (tile.height <= result.height) {
                            tile.height = clamp(result.height + 1, TILE_HEIGHT_LIMITS);
                        }
                    }
                }
            }
        }
    }

    if (!result.biomeTile) {
        result = generateLake(result, options);
    }

    if (result.biomeTile && waterTiles.includes(result.biomeTile.key)) {
        const { height } = result;
        const { neighbors, chunkTiles } = options;
        const isSea = result.biomeTile.key === EBiomeTile.Sea;

        const minHeight = findMinHeight(neighbors);

        if (minHeight && minHeight < height) {
            // Water biomes cannot have neighboring tiles below, and old "neighbor"@" tiles cannot be changed
            result.biomeTile = undefined;
        } else {
            // Last chance to change what we've generated before this water tile  we can lower the height or try generating a dependent biome again
            for (const tile of chunkTiles || []) {
                if (tile.height < height) {
                    tile.height = height;
                }

                if (isSea && !tile.biomeTile && !tile.biomeObject) {
                    const { biomeTile } = generateSand(tile, { neighbors: [result] });
                    tile.biomeTile = biomeTile;
                }
            }
        }
    }

    if (!result.biomeTile) {
        result = generateSand(result, options);
    }

    if (!result.biomeTile && !result.biomeObject) {
        result = generateClay(base, options);
    }

    return result;
};
