import { Observer } from '@/libs/cocos/components/Observer';
import { cached } from '@/libs/decorators/cached';
import { ccclass, property } from '@/libs/decorators/cocos';
import { tween, TweenEasing, UIOpacity } from 'cc';

@ccclass('UiOpacityAnimate')
export class UiOpacityAnimate extends Observer {
    @property({ min: 0, max: 255 })
    from = 0;

    @property({ min: 0, max: 255 })
    to = 255;

    @property
    duration = 1;

    @property
    delay = 0;

    @property
    easing: TweenEasing = 'cubicIn';

    @property
    once = true;

    @cached
    get opacityComponent() {
        return this.getComponent(UIOpacity) || this.addComponent(UIOpacity);
    }

    animate(value: number) {
        if (this.opacityComponent?.opacity !== value) {
            return tween(this.opacityComponent)
                .delay(this.delay)
                .to(this.duration, { opacity: value }, { easing: this.easing });
        }
    }

    hide() {
        if (!this.once && this.opacityComponent) {
            const animate = this.animate(this.from);

            if (animate) {
                animate.call(() => (this.node.active = false)).start();

                return;
            }
        }

        this.node.active = false;
    }

    onLoad() {
        this.animate(this.to)?.start();
    }

    onEnable() {
        if (!this.once && this.opacityComponent) {
            this.animate(this.to)?.start();
        }
    }

    onDisable() {
        if (!this.once && this.opacityComponent) {
            this.opacityComponent.opacity = this.from;
        }
    }
}
