import type { IVec3Like } from 'cc';
import { Vec3 } from 'cc';
import { EBiomeTile } from '@/features/Biome/data/EBiome';
import type { IBiomeBaseData } from '@/features/Biome/types/IBiomeData';
import { TILE_HEIGHT_LIMITS, TILE_SEA_LEVEL } from '@/features/Tile/data/consts';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { findWaterNeighbors } from '@/features/WorldGenerator/helpers/biomes/findWaterNeighbors';
import { generateNewLake } from '@/features/WorldGenerator/helpers/biomes/generateLake';
import { generateNewMountain } from '@/features/WorldGenerator/helpers/biomes/generateMountain';
import { generateSea } from '@/features/WorldGenerator/helpers/biomes/generateSea';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import { isNearWorldCenter } from '@/features/WorldGenerator/helpers/center/isNearCenter';
import { findMinHeight } from '@/features/WorldGenerator/helpers/findMinHeight';
import type { IGeneratorOptions } from '@/features/WorldGenerator/types/IGeneratorOptions';
import type { INeigborBiome } from '@/features/WorldGenerator/types/INeigborBiome';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { HexagonTile } from '@/libs/hexagon/HexagonTile';
import { clamp } from '@/libs/math/clamp';
import { toVec3 } from '@/libs/math/toVec3';
import { isRandomChance } from '@/libs/random/isRandomChance';
import { randomFromArray } from '@/libs/random/randomFromArray';
import { HEX_OPTIONS } from '@/features/Hex/data/consts';

function randomizeRiverSize() {
    return randomizeBiomeSize([0.2, 0.5, 0.2, 0.1]);
}

function randomizeRiverHeight(river: INeigborBiome<IBiomeBaseData>, diff: number) {
    const { height } = river.tile;

    if (river.biome.data?.to?.length > 1) {
        return height;
    }

    if (isRandomChance(0.1)) {
        return clamp(height + diff, [TILE_SEA_LEVEL, TILE_HEIGHT_LIMITS[1] - 1]);
    }

    return height;
}

function randomizeFrom(neighbors: Vec3[], options: IGeneratorOptions): IVec3Like | null {
    const candidates = neighbors.filter((neighbor) => {
        if (options.neighbors.some(({ coords }) => Vec3.equals(neighbor, coords))) {
            return false;
        }

        return true;
    });

    if (!candidates.length) {
        return null;
    }

    const from = randomFromArray(candidates);

    return from;
}

function randomizeTo(
    baseCoords: IVec3Like,
    from: IVec3Like,
    neighbors: IVec3Like[],
    options: IGeneratorOptions,
): IVec3Like[] {
    neighbors = neighbors.filter((neighbor) => {
        if (Vec3.equals(neighbor, from)) {
            return false;
        }

        if (options.neighbors.some(({ coords }) => Vec3.equals(neighbor, coords))) {
            return false;
        }

        return true;
    });

    const toLength = isRandomChance(0.04) ? 2 : 1;

    const to = [];

    // straight
    const straight = toVec3(from)
        .subtract3f(baseCoords.x, baseCoords.y, baseCoords.z)
        .negative()
        .add3f(baseCoords.x, baseCoords.y, baseCoords.z);

    neighbors = neighbors.filter((neighbor) => {
        if (Vec3.distance(straight, neighbor) < 2) {
            return true;
        }

        return false;
    });

    if (isRandomChance(0.6)) {
        const straightNeigbor = neighbors.find((neighbor) => Vec3.equals(straight, neighbor));

        if (straightNeigbor) {
            neighbors = neighbors.filter((neighbor) => !Vec3.equals(straightNeigbor, neighbor));
            to.push(straightNeigbor);
        }
    }

    for (let i = to.length; i < toLength; i++) {
        if (neighbors.length) {
            const coords = randomFromArray(neighbors);

            neighbors = neighbors.filter((neighbor) => !Vec3.equals(coords, neighbor));

            to.push(coords);
        }
    }

    return to;
}

export const generateRiver: TGenerator = (base, options) => {
    const { neighbors } = options;

    if (isNearWorldCenter(base.coords)) {
        return base;
    }

    const hex = new HexagonTile(null, toVec3(base.coords), HEX_OPTIONS);

    const rivers = findBiomeNeighbors(EBiomeTile.River, options);
    const waters = findWaterNeighbors(options);
    const water = waters.length ? waters[0] : null;

    if (!rivers.length) {
        const chance = 0.03 + 0.005 * waters.length;

        if (!isRandomChance(chance)) {
            return base;
        }

        const height = water?.tile.height || findMinHeight(neighbors) || base.height;

        if (height < TILE_SEA_LEVEL || height === TILE_HEIGHT_LIMITS[1]) {
            return base;
        }

        const from = water?.tile.coords || randomizeFrom(hex.neighbors, options);

        if (!from) {
            return base;
        }

        const to = randomizeTo(base.coords, from, hex.neighbors, options);

        if (to.every((toCoords) => neighbors.some((neighbor) => Vec3.equals(toCoords, neighbor.coords)))) {
            return base;
        }

        return {
            ...base,
            height,
            biomeTile: {
                id: guid(),
                key: EBiomeTile.River,
                size: randomizeRiverSize(),
                data: {
                    from,
                    to,
                },
            },
        };
    }

    const coords = toVec3(base.coords);

    const riverTo = rivers.find((river) => {
        const from = river.biome.data?.from;

        return from && Vec3.equals(coords, from);
    });

    if (riverTo) {
        const isEnding = riverTo.diff < 0;
        const height = randomizeRiverHeight(riverTo, 1);

        if (isEnding) {
            if (height > TILE_SEA_LEVEL && isRandomChance(0.5)) {
                base.height = height;

                return generateNewMountain(base, options);
            }

            if (isRandomChance(0.3)) {
                base.height = height;

                return generateNewLake(base, options);
            }

            base.height = clamp(riverTo.tile.height + 1, [TILE_SEA_LEVEL, TILE_HEIGHT_LIMITS[1] - 1]);

            return base;
        }

        return {
            ...base,
            height: height,
            biomeTile: {
                id: riverTo.biome.id,
                key: EBiomeTile.River,
                size: riverTo.biome.size,
                data: {
                    from: water?.tile.coords || randomizeFrom(hex.neighbors, options),
                    to: [riverTo.tile.coords],
                },
            },
        };
    }

    const riverFrom = rivers.find((river) => {
        const to = river.biome.data?.to;

        return to && to.some((toCoords: IVec3Like) => Vec3.equals(coords, toCoords));
    });

    if (riverFrom) {
        const isEnding = riverFrom.diff < 0;
        const height = randomizeRiverHeight(riverFrom, -1);

        if (isEnding) {
            if (height === TILE_SEA_LEVEL) {
                base.height = height;
                base = generateSea(base, options);

                if (base.biomeTile) {
                    return base;
                }
            }

            const minHeight = findMinHeight(options.neighbors);

            if (!minHeight || (minHeight < height && isRandomChance(0.5))) {
                base.height = height;
                return generateNewLake(base, options);
            }
        }

        let to: IVec3Like[] = [];

        if (!isEnding) {
            if (waters.length) {
                to = randomizeTo(
                    base.coords,
                    riverFrom.tile.coords,
                    waters.map(({ tile }) => tile.coords),
                    options,
                );
            }

            if (!to.length) {
                to = randomizeTo(base.coords, riverFrom.tile.coords, hex.neighbors, options);
            }
        }

        const riverFromToIndex = riverFrom.biome.data?.to?.findIndex((toCoords: IVec3Like) =>
            Vec3.equals(coords, toCoords),
        );

        return {
            ...base,
            height,
            biomeTile: {
                id: !riverFromToIndex ? riverFrom.biome.id : guid(),
                key: EBiomeTile.River,
                size: !riverFromToIndex ? riverFrom.biome.size : randomizeRiverSize(),
                data: {
                    from: riverFrom.tile.coords,
                    to,
                    ...(!riverFromToIndex
                        ? {}
                        : {
                              fromId: riverFrom.biome.id,
                          }),
                },
            },
        };
    }

    return base;
};
