import { EBiomeObject, EBiomeTile } from '@/features/Biome/data/EBiome';
import { findBiomeNeighbors } from '@/features/WorldGenerator/helpers/biomes/findBiomeNeighbors';
import { randomizeBiomeSize } from '@/features/WorldGenerator/helpers/biomes/randomizeBiomeSize';
import type { TGenerator } from '@/features/WorldGenerator/types/TGenerator';
import { guid } from '@/libs/helpers/guid';
import { isRandomChance } from '@/libs/random/isRandomChance';
import { randomFromRange } from '@/libs/random/randomFromRange';

export const generateRocks: TGenerator = (base, options) => {
    const mountains = findBiomeNeighbors(EBiomeObject.Mountain, options);
    const rocks = findBiomeNeighbors(EBiomeObject.Rocks, options);

    if (rocks.some((rock) => rock.diff <= 0)) {
        return base;
    }

    const rock = rocks[0];

    const diff = rock?.diff || 0;

    const chance = base.biomeTile?.key === EBiomeTile.Sand ? 0.1 : 0.05 + 0.2 * diff + 0.1 * mountains.length;

    if (!isRandomChance(chance)) {
        return base;
    }

    return {
        ...base,
        biomeObject: {
            id: rock?.biome.id || guid(),
            key: EBiomeObject.Rocks,
            rotate: randomFromRange([-180, 180]),
            scale: randomFromRange([0.7, 1.3], 1),
            size: rock?.biome.size || randomizeBiomeSize([0.5, 0.3, 0.15, 0.05]),
            data: isRandomChance(0.3)
                ? {
                      small: true,
                  }
                : undefined,
        },
    };
};
