import { Vec3 } from 'cc';
import { EBiomeTile } from '@/features/Biome/data/EBiome';
import type { IChunkData } from '@/features/Chunk/types/IChunkData';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { generateTile } from '@/features/WorldGenerator/helpers/generateTile';
import { sortCoords } from '@/features/WorldGenerator/helpers/sortCoords';
import { HexagonChunk } from '@/libs/hexagon/HexagonChunk';
import { HexagonTile } from '@/libs/hexagon/HexagonTile';
import { HEX_OPTIONS } from '@/features/Hex/data/consts';
import { state } from '@/states/state';

const BIOME_PRIORITY: Partial<Record<EBiomeTile, number>> = {
    [EBiomeTile.River]: 1,
};

function calcNeighborsScore(item: HexagonTile<null>) {
    const { tilesMap } = state.worldState;

    const neighbors = item.neighbors.map((coords) => tilesMap.get(coords)).filter(Boolean);

    return neighbors.reduce((result, neighbor) => {
        const biomeKey = neighbor?.self.data?.biomeTile?.key;
        const score = (biomeKey && BIOME_PRIORITY[biomeKey]) || 0;

        return result + score;
    }, 0);
}

export function generateChunk(center: Vec3): IChunkData {
    const chunk = new HexagonChunk(null, center, HEX_OPTIONS);

    const tiles: ITileData[] = [];

    const sortedCoords = sortCoords(chunk.tiles);

    sortedCoords
        .map((tileCoords) => new HexagonTile(null, tileCoords, HEX_OPTIONS))
        .sort((a, b) => calcNeighborsScore(b) - calcNeighborsScore(a))
        .forEach((fakeTile) => {
            const neighbors = [fakeTile.coords, ...fakeTile.neighbors].reduce<ITileData[]>((result, nearCoords) => {
                const { tilesMap } = state.worldState;

                const tile = tilesMap.get(nearCoords);

                if (tile) {
                    result.push(tile.self.data);
                } else {
                    const alreadyAdded = tiles.find(({ coords }) => Vec3.equals(nearCoords, coords));

                    if (alreadyAdded) {
                        result.push(alreadyAdded);
                    }
                }

                return result;
            }, []);

            tiles.push(generateTile(fakeTile.coords, { neighbors, chunkTiles: tiles }));
        });

    return {
        coords: center,
        tiles,
    };
}
