import { IResourceCardProps, ResourceCard } from '@/features/Resource/components/ResourceCard';
import { EResource } from '@/features/Resource/data/EResource';
import { factoryTemplateComponent } from '@/libs/cocos/helpers/factoryTemplateComponent';
import { ccclass, property } from '@/libs/decorators/cocos';
import { AbstractScreen } from '@/routes/AbstractRoute/components/AbstractScreen';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { ResourcesRoute } from '@/routes/ResourcesRoute/ResourcesRoute';
import { router } from '@/routes/router';
import { state } from '@/states/state';
import { IReactionDisposer, action, observable, reaction } from 'vendors/mobx';
import { Node, UITransform } from 'cc';

const DEFAULT_CARD_HEIGHT = 148;
const PADDING_DIFF = 2 * 16 + 2 * 16;

@ccclass('ResourcesScreen')
export class ResourcesScreen extends AbstractScreen<ResourcesRoute> {
    static template: TResourceTemplate = 'ResourcesScreen';

    readonly routeName = ERouteName.Resources;

    @property(Node)
    resourcesNode: Node;

    @property(UITransform)
    resourcesWrapperTransform: UITransform;

    @observable.shallow
    resourceCards: ResourceCard[] = [];

    protected disposeResourcesListen: IReactionDisposer;

    @action.bound
    render() {
        this.resourcesNode.removeAllChildren();

        const { resources } = state.resourcesState;

        this.resourceCards = Object.entries(resources).sort(([,a], [,b]) => a - b).map(([resource, count]) => {
            const card = factoryTemplateComponent<IResourceCardProps, ResourceCard>(ResourceCard, this.resourcesNode, {
                resource: resource as EResource,
                count,
            });

            return card;
        });

        const dimension = this.resourceCards[0].node.getComponent(UITransform)?.height ?? DEFAULT_CARD_HEIGHT;

        this.resourcesWrapperTransform.height =
            Math.ceil(this.resourceCards.length / Math.floor(this.resourcesWrapperTransform.width / dimension)) *
                dimension +
            PADDING_DIFF;
    }

    onCancelClick() {
        const isNavigated = router.navigateBack();

        if (!isNavigated) {
            router.navigate(ERouteName.World);
        }
    }
}
