export function promiseTimeout<T>(ms: number, promise: Promise<T>, onTimeout?: () => void): Promise<T> {
    return new Promise(function (resolve, reject) {
        const timer = setTimeout(function () {
            onTimeout?.();

            reject(new Error('Promise Timed Out'));
        }, ms);

        promise
            .then(function (res) {
                clearTimeout(timer);
                resolve(res);
            })
            .catch(function (err) {
                clearTimeout(timer);
                reject(err);
            });
    });
}
