import { GameState } from '@/states/GameState/GameState';
import { makeObservable } from 'vendors/mobx';

export abstract class AbstractState<IData = {}> {
    protected state: GameState;

    constructor(state: GameState) {
        this.state = state;
    }

    destroy() {
        // no-op
    }

    initialize(_data: IData): this {
        makeObservable(this);

        return this;
    }

    abstract serialize(): IData;
}
