import { action, computed, makeObservable, observable } from 'vendors/mobx';
import type { IWorldRouteOptions } from '@/routes/WorldRoute/WorldRoute';
import { WorldRoute } from '@/routes/WorldRoute/WorldRoute';
import { IPauseRouteOptions, PauseRoute } from '@/routes/PauseRoute/PauseRoute';
import { AbstractRoute } from '@/routes/AbstractRoute/AbstractRoute';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { BuildRoute, IBuildRouteOptions } from '@/routes/BuildRoute/BuildRoute';
import { IResourcesRouteOptions, ResourcesRoute } from '@/routes/ResourcesRoute/ResourcesRoute';

const MAX_HISTORY_LENGTH = 10;
const NAVIGATION_DEBOUNCE = 200;

const routes: Record<ERouteName, { new (): AbstractRoute<unknown> }> = {
    [ERouteName.World]: WorldRoute,
    [ERouteName.Pause]: PauseRoute,
    [ERouteName.Build]: BuildRoute,
    [ERouteName.Resources]: ResourcesRoute,
};

class Router {
    private history: { route: ERouteName; options?: unknown }[] = [{ route: ERouteName.Pause }];

    constructor() {
        makeObservable(this);
    }

    @computed
    get routeName() {
        return this.route.name;
    }

    // default route is Pause
    @observable route: AbstractRoute<unknown> = new PauseRoute().initialize({ isIntro: true });

    private navigateTimeout: NodeJS.Timeout | null = null;

    navigate(route: ERouteName.Build, options?: IBuildRouteOptions): void;
    navigate(route: ERouteName.Pause, options?: IPauseRouteOptions): void;
    navigate(route: ERouteName.World, options?: IWorldRouteOptions): void;
    navigate(route: ERouteName.Resources, options?: IResourcesRouteOptions): void;
    navigate(route: ERouteName, options: unknown, navigateOptions: { back?: boolean }): void;
    navigate(route: ERouteName, options: unknown, navigateOptions?: { back?: boolean }) {
        if (this.navigateTimeout) {
            clearTimeout(this.navigateTimeout);
            this.navigateTimeout = null;
        }

        this.navigateTimeout = setTimeout(
            action(() => {
                if (!navigateOptions?.back && this.history.length) {
                    this.history[this.history.length - 1].options = this.route.serialize();
                }

                this.route.destroy();

                const Route = routes[route];
                this.route = new Route().initialize(options);

                if (navigateOptions?.back) {
                    this.history.pop();
                } else {
                    const historyLength = this.history.push({ route, options });

                    if (historyLength > MAX_HISTORY_LENGTH) {
                        this.history = this.history.slice(MAX_HISTORY_LENGTH - historyLength);
                    }
                }

                this.navigateTimeout = null;
            }),
            NAVIGATION_DEBOUNCE,
        );
    }

    navigateBack() {
        const record = this.history[this.history.length - 2];

        if (!record) {
            return false;
        }

        this.navigate(record.route, record.options, { back: true });

        return true;
    }

    getRoute<T extends AbstractRoute>(name: ERouteName): T | null {
        if (this.routeName === name) {
            return this.route as T;
        }

        return null;
    }
}

export const router = new Router();
