import { BuildingsDB } from '@/features/Building/data/BuildingsDB';
import { EBuilding } from '@/features/Building/data/EBuilding';
import { IBuildingData } from '@/features/Building/types/IBuildingData';
import { DiWorld } from '@/features/Di/components/DiWorld';
import { Tile } from '@/features/Tile/components/Tile';
import { fromVec3ToVec2 } from '@/libs/math/fromVec3ToVec2';
import { AbstractRoute } from '@/routes/AbstractRoute/AbstractRoute';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { BuildScreen } from '@/routes/BuildRoute/components/BuildScreen';
import { state } from '@/states/state';
import { Vec3 } from 'cc';
import { action, computed, IReactionDisposer, observable, reaction } from 'vendors/mobx';

export interface IBuildRouteOptions {
    building?: EBuilding | null;
    buildingCoords?: Vec3 | null;
}

export class BuildRoute extends AbstractRoute<IBuildRouteOptions> implements IBuildRouteOptions {
    readonly name = ERouteName.Build;
    readonly screen = BuildScreen;

    @observable building: EBuilding | null = null;
    @observable buildingRotate?: number;
    @observable.ref buildingCoords: Vec3 | null = null;

    @computed
    get buildingTile(): Tile | null {
        const { tilesMap } = state.worldState;

        if (this.buildingCoords) {
            return tilesMap.get(this.buildingCoords)?.self || null;
        }

        return null;
    }

    @computed
    get buildingData(): Optional<IBuildingData> {
        if (!this.building) {
            return undefined;
        }

        return {
            key: this.building,
            rotate: this.buildingRotate,
            progress: 0,
        };
    }

    @computed
    get buildingDb() {
        return (this.building && BuildingsDB[this.building]) || null;
    }

    @computed
    get isChunkMode() {
        return !this.isBuildingPositionMode && !this.isBuildingPreviewMode;
    }

    @computed
    get isBuildingPositionMode() {
        return Boolean(this.building && !this.isBuildingPreviewMode);
    }

    @computed
    get isBuildingPreviewMode() {
        return Boolean(this.building && this.buildingCoords);
    }

    zoomIn() {
        if (this.buildingTile) {
            const { worldCamera: camera } = DiWorld;

            camera.navigate({
                zooming: -0.5,
                position: fromVec3ToVec2(this.buildingTile.node.getWorldPosition()),
            });
        }
    }

    zoomOut() {
        const { worldCamera: camera } = DiWorld;

        if (camera.zooming < 0) {
            camera.navigate({
                zooming: 0,
            });
        }
    }

    serialize(): IBuildRouteOptions {
        return {
            building: this.building,
            buildingCoords: this.buildingCoords,
        };
    }

    disposeBuildingListen?: IReactionDisposer;
    disposeBuildingPositionListen?: IReactionDisposer;

    initialize(options: IBuildRouteOptions) {
        super.initialize(options);

        this.disposeBuildingListen = reaction(
            () => this.building,
            action(() => {
                this.buildingCoords = null;
                this.zoomOut();
            })
        );

        this.disposeBuildingPositionListen = reaction(
            () => this.buildingCoords,
            () => {
                this.zoomIn();
            }
        );

        return this;
    }

    destroy() {
        this.disposeBuildingListen?.();
        this.disposeBuildingPositionListen?.();
    }
}
