import type { Vec3, IVec3Like } from 'cc';
import type { ObservableMap } from 'vendors/mobx';
import { action, computed, observable } from 'vendors/mobx';
import type { Hexagon, HexagonClass, HexagonOptions } from '@/libs/hexagon/Hexagon';

export class HexagonMap<T, H extends Hexagon<T>> {
    map = observable.map<number, ObservableMap<number, ObservableMap<number, H>>>();

    @observable maxX = 0;

    @observable maxZ = 0;

    @observable minX = 0;

    @observable minZ = 0;

    options: HexagonOptions;

    Hex: HexagonClass<T, H>;

    constructor(Hex: HexagonClass<T, H>, options: HexagonOptions) {
        this.options = options;
        this.Hex = Hex;
    }

    @computed
    get values() {
        const result: H[] = [];

        for (const yMap of this.map.values()) {
            for (const zMap of yMap.values()) {
                for (const val of zMap.values()) {
                    result.push(val);
                }
            }
        }

        return result;
    }

    @action.bound
    get(coords: IVec3Like) {
        const { x, y, z } = coords;

        let yMap = this.map.get(x);

        if (!yMap) {
            this.map.set(x, (yMap = observable.map()));
        }

        let zMap = yMap.get(y);

        if (!zMap) {
            yMap.set(y, (zMap = observable.map()));
        }

        return zMap.get(z);
    }

    @action.bound
    add(coords: Vec3, item: T) {
        const hex = new this.Hex(item, coords, this.options);

        if (hex.positionX > this.maxX) {
            this.maxX = hex.positionX;
        } else if (hex.positionX < this.minX) {
            this.minX = hex.positionX;
        }

        if (hex.positionZ > this.maxZ) {
            this.maxZ = hex.positionZ;
        } else if (hex.positionZ < this.minZ) {
            this.minZ = hex.positionZ;
        }

        const { x, y, z } = hex.coords;

        let yMap = this.map.get(x);

        if (!yMap) {
            this.map.set(x, (yMap = observable.map()));
        }

        let zMap = yMap.get(y);

        if (!zMap) {
            yMap.set(y, (zMap = observable.map()));
        }

        const old = zMap.get(z);

        if (old) {
            (old as any).destroy?.();
        }

        zMap.set(z, hex);

        return hex;
    }
}
