import { BuildingsDB } from '@/features/Building/data/BuildingsDB';
import { EResource } from '@/features/Resource/data/EResource';
import { ResourcesStart } from '@/features/Resource/data/ResourcesStart';
import { AbstractState } from '@/states/AbstractState/AbstractState';
import { computed } from 'vendors/mobx';

type IResourceReason = [EResource, number];

export interface IResourcesState {}

export class ResourcesState extends AbstractState<IResourcesState> {
    @computed
    get resources() {
        const resources: Partial<Record<EResource, number>> = {
            ...ResourcesStart,
        };

        const applyReasons = (reasons: IResourceReason[]) => {
            for (const [resource, value] of reasons) {
                resources[resource] = (resources[resource] || 0) + value;
            }
        };

        applyReasons(this.resourcesFromBuildings);
        applyReasons(this.priceFromBuildQueue);

        return resources;
    }

    @computed
    get resourcesFromBuildings() {
        const { worldState } = this.state;

        return worldState.activeBuildings.reduce<IResourceReason[]>((result, building) => {
            const {buildingDb} = building;

            for (const [resource, value] of Object.entries(buildingDb.resources)) {
                result.push([resource as EResource, value] as const);
            }

            return result;
        }, []);
    }

    @computed
    get priceFromBuildQueue() {
        const { worldState } = this.state;

        return worldState.buildQueue.reduce<IResourceReason[]>((result, building) => {
            const { buildingDb } = building;

            for (const [resource, value] of Object.entries(buildingDb.buildOptions!.price)) {
                result.push([resource as EResource, value] as const);
            }

            return result;
        }, []);
    }

    serialize(): IResourcesState {
        return {};
    }

    override initialize(data: IResourcesState) {
        return super.initialize(data);
    }
}
