import { ccclass } from '@/libs/decorators/cocos';
import { i18n } from '@/libs/i18n/i18n';
import { Label } from 'cc';

@ccclass('UiLabel')
export class UiLabel extends Label {
    get string() {
        return i18n(this._string);
    }

    set string(value: string) {
        const set = Object.getOwnPropertyDescriptor(Label.prototype, 'string')?.set;

        set?.call(this, value);
    }
}
