type TEnum<T = string> = {
    [id: string]: T | string;
    [nu: number]: string;
};

type TCCEnumProp<T> = { name: string; value: T };

type TCCEnum<T = number> = {
    __enums__: TCCEnumProp<T>[];
} & {
    [key: string]: T;
};

export function toCocosEnum<T extends TEnum>(enumValue: T) {
    const newEnum: TCCEnum<string> = {
        __enums__: [],
    } as unknown as TCCEnum<string>;

    Object.keys(enumValue).forEach((key) => {
        const value = enumValue[key];

        newEnum[key] = value;

        newEnum.__enums__.push({ name: key, value });
    });

    return newEnum;
}
