import { Color, Sprite, SpriteFrame, Node } from 'cc';

export function factorySprite(node: Node, spriteFrame: SpriteFrame | null, color: Color = Color.WHITE) {
    const component = node.addComponent(Sprite);

    component.color = color.clone();
    component.trim = false;
    component.sizeMode = Sprite.SizeMode.CUSTOM;
    component.type = Sprite.Type.SIMPLE;
    if (spriteFrame) {
        component.spriteFrame = spriteFrame;
    }

    return component;
}
