import type { IVec3Like } from 'cc';
import { Vec3 } from 'cc';
import { BIOME_OBJECT_KEYS, type EBiome, type EBiomeObject } from '@/features/Biome/data/EBiome';
import type { Tile } from '@/features/Tile/components/Tile';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { HexagonTile } from '@/libs/hexagon/HexagonTile';
import { toVec3 } from '@/libs/math/toVec3';
import { HEX_OPTIONS } from '@/features/Hex/data/consts';
import { state } from '@/states/state';

export function getBiomeActualSize(coords: IVec3Like, biomeId: string, biomeKey: EBiome, neighbors: ITileData[] = []) {
    let size = 0;
    let bordersSize = 0;

    const isObject = BIOME_OBJECT_KEYS.includes(biomeKey as EBiomeObject);
    const visited = new Map<string, true>();

    const calculate = (tileCoords: IVec3Like) => {
        const key = toVec3(tileCoords).toString();

        if (visited.has(key)) {
            return;
        }

        visited.set(key, true);

        const { tilesMap } = state.worldState;

        let tile: HexagonTile<Tile | null> | undefined = tilesMap.get(tileCoords);
        let tileData = tile?.self?.data;

        if (!tileData) {
            tileData = neighbors.find((nearTile) => Vec3.equals(nearTile.coords, tileCoords));

            if (tileData) {
                tile = new HexagonTile(null, toVec3(tileCoords), HEX_OPTIONS);
            }
        }

        if (!tile || !tileData) {
            bordersSize++;

            return;
        }

        const biome = isObject ? tileData.biomeObject : tileData.biomeTile;

        if (biome?.key === biomeKey && biome.id === biomeId) {
            size++;

            tile.neighbors.forEach((nearCoords) => {
                calculate(nearCoords);
            });
        }
    };

    calculate(coords);

    neighbors.forEach((tile) => calculate(tile.coords));

    return { size, bordersSize };
}
