import { ccclass } from '@/libs/decorators/cocos';
import { AbstractScreen } from '@/routes/AbstractRoute/components/AbstractScreen';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { router } from '@/routes/router';
import { WorldRoute } from '@/routes/WorldRoute/WorldRoute';
import { state } from '@/states/state';
import { action } from 'vendors/mobx';

@ccclass('WorldScreen')
export class WorldScreen extends AbstractScreen<WorldRoute> {
    static template: TResourceTemplate = 'WorldScreen';

    readonly routeName = ERouteName.World;

    @action.bound
    onTurnClick() {
        state.turnState.turn++;
    }

    onPauseClick() {
        router.navigate(ERouteName.Pause);
    }

    onBuildClick() {
        router.navigate(ERouteName.Build);
    }

    onResourcesClick() {
        router.navigate(ERouteName.Resources);
    }
}
