import { waitForFrame } from '@/libs/async/waitForFrame';
import { getAllComponents } from '@/libs/cocos/helpers/getAllComponents';
import { Component, Layout, Widget } from 'cc';

export async function updateNodeLayout(component: Component) {
    await waitForFrame();

    if (!component.node || !component.node.activeInHierarchy) {
        return;
    }

    getAllComponents(component.node, Layout).forEach((layout) => layout.updateLayout());
    getAllComponents(component.node, Widget).forEach((widget) => widget.updateAlignment());
}
