import { BuildCard, IBuildCardProps } from '@/features/BuildCard/components/BuildCard';
import { EBuilding } from '@/features/Building/data/EBuilding';
import { waitForFrame } from '@/libs/async/waitForFrame';
import { factoryTemplateComponent } from '@/libs/cocos/helpers/factoryTemplateComponent';
import { ccclass, property } from '@/libs/decorators/cocos';
import { AbstractScreen } from '@/routes/AbstractRoute/components/AbstractScreen';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { BuildRoute } from '@/routes/BuildRoute/BuildRoute';
import { router } from '@/routes/router';
import { Node } from 'cc';
import { action, computed, observable, runInAction } from 'vendors/mobx';

const BUILDINGS: EBuilding[] = [EBuilding.Lumberjack, EBuilding.Sawmill, EBuilding.Quarry];

@ccclass('BuildScreen')
export class BuildScreen extends AbstractScreen<BuildRoute> {
    static template: TResourceTemplate = 'BuildScreen';

    readonly routeName = ERouteName.Build;

    @property(Node)
    buildingsNode: Node;

    @observable.shallow
    buildCards: BuildCard[] = [];

    onLoad() {
        for (const building of BUILDINGS) {
            const card = factoryTemplateComponent<IBuildCardProps, BuildCard>(BuildCard, this.buildingsNode, { building, onClick: this.onSelectBuilding });

            this.buildCards.push(card);
        }
    }

    @action.bound
    onSelectBuilding(building: EBuilding) {
        if (this.route) {
            this.route.building = this.route.building === building ? null : building;
        }
    }

    @action.bound
    onBuildClick() {
        if (!this.route) {
            return;
        }

        const tile = this.route.buildingTile;

        if (tile) {
            this.route.zoomOut();
            router.navigateBack();

            if (tile.buildingPreview) {
                tile.buildingPreview.node.active = false;
            }

            tile.data.building = this.route.buildingData;
        }
    }

    onCancelClick() {
        const isNavigated = router.navigateBack();

        if (!isNavigated) {
            router.navigate(ERouteName.World);
        }
    }

    @computed
    get isBuildingSelected() {
        return Boolean(this.route?.building);
    }

    @computed
    get isReadyToBuild() {
        return this.route?.isBuildingPreviewMode;
    }

    render() {
        const selectedBuilding = this.route?.building;

        runInAction(() => {
            for (const card of this.buildCards) {
                card.selected = card.building === selectedBuilding;
            }
        });
    }
}
