import { EBiomeObject } from '@/features/Biome/data/EBiome';
import { EBiomeSize } from '@/features/Biome/data/EBiomeSize';
import type { IBiomeObjectData } from '@/features/Biome/types/IBiomeData';

export const biomeObjectPrefabs: Partial<Record<EBiomeObject, (biome: IBiomeObjectData) => TResourcePrefab>> = {
    [EBiomeObject.Mountain]: () => 'mountain',
    [EBiomeObject.Forest]: () => 'forest',
    [EBiomeObject.ForestRocks]: ({ size }) => (size === EBiomeSize.Tiny ? 'forest_rocks_tiny' : 'forest_rocks_small'),
    [EBiomeObject.Rocks]: ({ data }) => (data?.small ? 'rocks_small' : 'rocks'),
    [EBiomeObject.Hill]: () => 'hill',
};
