import { AbstractRoute } from '@/routes/AbstractRoute/AbstractRoute';
import { ERouteName } from '@/routes/AbstractRoute/data/ERouteName';
import { PauseScreen } from '@/routes/PauseRoute/components/PauseScreen';

export interface IPauseRouteOptions {
    isIntro?: boolean;
}

export class PauseRoute extends AbstractRoute<IPauseRouteOptions> implements IPauseRouteOptions {
    readonly name = ERouteName.Pause;
    readonly screen = PauseScreen;
    hasUiOverlay = true;
    hasUiHeader = false;

    isIntro?: boolean;

    serialize(): IPauseRouteOptions {
        return {
            isIntro: this.isIntro,
        };
    }
}
