import { Vec3 } from 'cc';
import type { IBiomeProps } from '@/features/Biome/components/Biome';
import { Biome } from '@/features/Biome/components/Biome';
import { MAX_BIOME_TILE_HEIGHT } from '@/features/Biome/data/consts';
import { EBiomeTile } from '@/features/Biome/data/EBiome';
import type { IBiomeTileData } from '@/features/Biome/types/IBiomeData';
import { TILE_HEIGHT_MULTIPLIER } from '@/features/Tile/data/consts';
import { createPrefabNode } from '@/libs/cocos/helpers/createPrefabNode';
import { cached } from '@/libs/decorators/cached';
import { ccclass } from '@/libs/decorators/cocos';
import { assets } from '@/features/Assets/data/assets';

const MIN_DIRT_HEIGHT = 3;

export type TBiomeTileProps = IBiomeProps<IBiomeTileData | undefined>;

@ccclass('BiomeTile')
export class BiomeTile extends Biome<IBiomeTileData | undefined> {
    @cached
    get prefab(): TResourcePrefab {
        return 'hex';
    }

    @cached
    get material(): TResourceMaterial {
        if (this.data?.key === EBiomeTile.Clay) {
            return 'clay';
        }

        if (this.data?.key === EBiomeTile.Sand) {
            return 'sand';
        }

        return 'grass';
    }

    @cached
    get biomeY(): number {
        return Math.min(this.tile.data.height, MAX_BIOME_TILE_HEIGHT) * TILE_HEIGHT_MULTIPLIER;
    }

    @cached
    get dirtY() {
        return this.tile.groundY - this.rockY - this.biomeY;
    }

    @cached
    get rockY() {
        if (this.tile.data.height - MAX_BIOME_TILE_HEIGHT < MIN_DIRT_HEIGHT) {
            return 0;
        }

        return this.tile.data.baseHeight * TILE_HEIGHT_MULTIPLIER;
    }

    @cached
    get receiveShadows() {
        return true;
    }

    @cached
    get castShadows() {
        return true;
    }

    protected createRenderNode() {
        if (!this.biomeY) {
            return null;
        }

        const prefab = assets.getPrefabs(this.prefab);
        const material = assets.getMaterials(this.material);

        return createPrefabNode(prefab, {
            parent: this.node,
            materials: [material],
            position: new Vec3(0, this.rockY + this.dirtY, 0),
            scale: new Vec3(1, this.biomeY, 1),
            receiveShadows: this.receiveShadows,
            castShadows: this.castShadows,
            onTap: this.onTap,
        });
    }

    onLoad() {
        const [hexBase, hex] = assets.getPrefabs(['hex_base', 'hex']);
        const [rock, dirt] = assets.getMaterials(['rock', 'dirt']);

        // ROCK
        createPrefabNode(hexBase, {
            parent: this.node,
            materials: [rock],
            position: new Vec3(0, -1, 0),
            scale: new Vec3(1, 1 + this.rockY, 1),
        });

        createPrefabNode(this.biomeY ? hexBase : hex, {
            parent: this.node,
            materials: [dirt],
            position: new Vec3(0, this.rockY, 0),
            scale: new Vec3(1, this.dirtY, 1),
            receiveShadows: this.receiveShadows,
            castShadows: this.castShadows,
        });
    }
}
