import { isPromise } from '@/libs/helpers/isPromise';

export function createFactory<T>(init: () => Promise<T>): () => Promise<T> | T;
export function createFactory<T>(init: () => T): () => T;
export function createFactory<T>(init: () => Promise<T> | T): () => Promise<T> | T {
    let instance: T | null = null;

    return () => {
        if (!instance) {
            const value = init();

            if (isPromise(value)) {
                return Promise.resolve(init()).then((v) => {
                    instance = v;

                    return instance;
                });
            }

            instance = value;
        }

        return instance;
    };
}
