import { Vec3 } from 'cc';
import { action, computed, observable } from 'vendors/mobx';
import type { TBiomeObjectProps } from '@/features/Biome/components/BiomeObject';
import { BiomeObject } from '@/features/Biome/components/BiomeObject';
import type { TBiomeTileProps } from '@/features/Biome/components/BiomeTile';
import { BiomeTile } from '@/features/Biome/components/BiomeTile';
import { biomeTiles } from '@/features/Biome/data/biomeTiles';
import type { IBuildingProps } from '@/features/Building/components/Building';
import { Building } from '@/features/Building/components/Building';
import type { Chunk } from '@/features/Chunk/components/Chunk';
import type { ITileTestProps } from '@/features/Tile/components/TileTest';
import { TileTest } from '@/features/Tile/components/TileTest';
import { TILE_HEIGHT_MULTIPLIER } from '@/features/Tile/data/consts';
import type { ITileData } from '@/features/Tile/types/ITileData';
import { Observer } from '@/libs/cocos/components/Observer';
import { factoryComponent } from '@/libs/cocos/helpers/factoryComponent';
import { cached } from '@/libs/decorators/cached';
import { ccclass } from '@/libs/decorators/cocos';
import type { HexagonTile } from '@/libs/hexagon/HexagonTile';
import { getBuildRoute } from '@/routes/BuildRoute/helpers/getBuildRoute';
import { toVec3 } from '@/libs/math/toVec3';
import { BuildingPreview } from '@/features/Building/components/BuildingPreview';
import { state } from '@/states/state';

export interface ITileProps {
    coords: Vec3;
    data: ITileData;
    chunk: Chunk;
}

@ccclass('Tile')
export class Tile extends Observer {
    hex: HexagonTile<Tile>;

    chunk: Chunk;

    @observable data: ITileData;

    tileTest?: TileTest;
    biomeTile?: BiomeTile;
    buildingPreview?: BuildingPreview;
    building?: Building;
    biomeObject?: BiomeObject;

    @computed
    get neighborsTiles() {
        return this.hex.neighbors.reduce<Tile[]>((result, coords) => {
            const { tilesMap } = state.worldState;

            const hex = tilesMap.get(coords);

            if (hex) {
                result.push(hex.self)
            }

            return result;
        }, []);
    }

    @cached
    get groundY() {
        return this.data.height * TILE_HEIGHT_MULTIPLIER + 1;
    }

    @cached
    get biomeTileComponent() {
        return (this.data.biomeTile && biomeTiles[this.data.biomeTile.key]) || BiomeTile;
    }

    @computed
    get previewBuilding() {
        const route = getBuildRoute();

        if (route?.buildingTile === this) {
            return route.buildingData;
        }

        return null;
    }

    @action.bound
    initialize({ coords, chunk, data }: ITileProps) {
        const { tilesMap } = state.worldState;

        this.chunk = chunk;
        this.data = data;
        this.hex = tilesMap.add(coords, this);
    }

    @action.bound
    onTap() {
        if (this.tileTest?.isActive) {
            const route = getBuildRoute();

            if (route) {
                route.buildingCoords = toVec3(this.data.coords);
            }

            return;
        }

        if (this.previewBuilding) {
            const route = getBuildRoute();

            if (route) {
                route.buildingRotate = ((route.buildingRotate || 0) + 60) % 360;
            }

            return;
        }
    }

    private renderBiomeTile() {
        const props: TBiomeTileProps = {
            tile: this,
            data: this.data.biomeTile,
            onTap: this.onTap,
        };

        if (this.biomeTile && !(this.biomeTile instanceof this.biomeTileComponent)) {
            (this.biomeTile as BiomeTile).node.destroy();

            this.biomeTile = undefined;
        }

        if (!this.biomeTile) {
            this.biomeTile = factoryComponent(this.biomeTileComponent, this.node, props);
        } else {
            this.biomeTile.initialize(props)
        }
    }

    private renderBuildingPreview() {
        if (this.previewBuilding) {
            const props: IBuildingProps = {
                tile: this,
                onTap: this.onTap,
                data: this.previewBuilding,
            };

            if (!this.buildingPreview) {
                this.buildingPreview = factoryComponent(BuildingPreview, this.node, props);
            } else {
                this.buildingPreview.initialize(props);
            }
        } else {
            if (this.buildingPreview) {
                this.buildingPreview.node.destroy();
                this.buildingPreview = undefined;
            }
        }
    }

    private renderBuilding() {
        if (this.data.building) {
            const props: IBuildingProps = {
                data: this.data.building,
                tile: this,
                onTap: this.onTap,
            };

            if (!this.building) {
                this.building = factoryComponent(Building, this.node, props);
            } else {
                this.building.initialize(props)
            }
        } else {
            if (this.building) {
                this.building.node.destroy();
                this.building = undefined;
            }
        }
    }

    private renderBiomeObject() {
        if (this.data.biomeObject) {
            const props: TBiomeObjectProps = {
                data: this.data.biomeObject,
                tile: this,
                onTap: this.onTap,
            };

            if (!this.biomeObject) {
                this.biomeObject = factoryComponent(BiomeObject, this.node, props)
            } else {
                this.biomeObject.initialize(props);
            }
        } else {
            if (this.biomeObject) {
                this.biomeObject.node.destroy();
                this.biomeObject = undefined;
            }
        }
    }

    private renderTileTest() {
        if (!this.tileTest) {
            this.tileTest = factoryComponent<ITileTestProps, TileTest>(TileTest, this.node, {
                tile: this,
            });
        }
    }

    render() {
        this.renderBiomeTile();
        this.renderBuildingPreview();
        this.renderBuilding();
        this.renderBiomeObject();
        this.renderTileTest();
    }

    onLoad() {
        this.node.setWorldPosition(new Vec3(this.hex.positionX, 0, this.hex.positionZ));
    }
}
