import { EBiomeObject, EBiomeTile } from '@/features/Biome/data/EBiome';
import { EBuilding } from '@/features/Building/data/EBuilding';
import { EResource } from '@/features/Resource/data/EResource';
import { Tile } from '@/features/Tile/components/Tile';
import { tileHasBiomeObjects } from '@/features/Tile/helpers/tileHasBiomeObjects';
import { tileNotWater } from '@/features/Tile/helpers/tileNotWater';
import { tileWithoutObject } from '@/features/Tile/helpers/tileWithoutObject';
import { hexColor } from '@/libs/helpers/hexColor';
import { Color } from 'cc';

export interface IBuildingDb {
    title: string;
    prefab: TResourcePrefab;
    buildOptions?: {
        iconColor: Color;
        iconScale?: number;
        turns: number;
        price: Partial<Record<EResource, number>>;
    };
    resources: Partial<Record<EResource, number>>;
    tileTest?: ((tile: Tile) => boolean);
}

export const BuildingsDB: Record<EBuilding, IBuildingDb> = {
    [EBuilding.Castle]: {
        title: 'Castle',
        prefab: 'building_castle',
        resources: {
            [EResource.Log]: +2,
        },
    },
    [EBuilding.Lumberjack]: {
        title: 'Lumberjack',
        prefab: 'building_tent',
        resources: {
            [EResource.Log]: +1,
        },
        buildOptions: {
            iconColor: hexColor('D5B089', 0.5),
            turns: 1,
            price: {
                [EResource.Log]: -1,
            },
        },
        tileTest(tile) {
            return tileWithoutObject(tile) && tileNotWater(tile) && tile.neighborsTiles.some((neighbor) => {
                return tileHasBiomeObjects(neighbor, [EBiomeObject.Forest]);
            });
        },
    },
    [EBuilding.Quarry]: {
        title: 'Quarry',
        prefab: 'building_scaffolding',
        resources: {
            [EResource.Stone]: +1,
        },
        buildOptions: {
            iconColor: hexColor('708695', 0.5),
            turns: 1,
            price: {
                [EResource.Log]: -1,
                [EResource.Plank]: -1,
            },
        },
        tileTest(tile) {
            return tileWithoutObject(tile) && tileNotWater(tile) && tile.neighborsTiles.some((neighbor) => {
                return tileHasBiomeObjects(neighbor, [EBiomeObject.Mountain, EBiomeObject.Rocks]);
            });
        },
    },
    [EBuilding.Sawmill]: {
        title: 'Sawmill',
        prefab: 'building_lumbermill',
        resources: {
            [EResource.Log]: -1,
            [EResource.Plank]: +1,
        },
        buildOptions: {
            iconColor: hexColor('D5B089', 0.5),
            turns: 3,
            price: {
                [EResource.Log]: -2,
            },
        },
        tileTest(tile) {
            return tileWithoutObject(tile) && tileNotWater(tile);
        },
    },
};
